.header-menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  margin-right: -10px;

  > * {
    margin-left: 20px;
    padding: 10px;
  }

  @include vp--1023 {
    margin-right: 0;
  }

  @include vp--599 {
    margin-right: -10px;

    > * {
      margin-left: 0;
    }
  }
}

.header-menu__location {
  width: 180px;

  @include vp--1023 {
    display: none;
  }
}

.header-menu__location-select {
  z-index: 11;

  margin-bottom: 0;
}

.header-menu__btns {
  position: relative;
  display: flex;

  padding-left: 0;

  > * {
    &:not(:first-child) {
      margin-left: 30px;
    }
  }

  @include vp--599 {
    position: static;
    padding-right: 5px;

    > * {
      &:not(:first-child) {
        margin-left: 5px;
      }
    }
  }
}

.header-menu__search-bth {
  @include reset-btn;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;

  color: $color-kelly-green;

  transition: color $trans--300;

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
    }
  }
}

.header-menu__search-form {
  overflow: hidden;
  width: 0;
  opacity: 0;

  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  background-color: $color-default-white;

  transition:
    opacity $trans--300,
    width 0s linear 0.3s;

  &:focus-within {
    width: 100%;
    opacity: 1;

    transition: opacity $trans--300;
  }

  @include vp--599 {
    top: 100%;
  }
}

.header-menu__search-input {
  margin-bottom: 0;

  label {
    input {
      width: 100%;
      margin: 10px;
      margin-left: 0;
      padding: 10px 40px;

      border: none;
    }
  }

  @include vp--599 {
    label {
      input {
        margin: 0;
        padding-left: 50px;

        font-size: 14px;
        line-height: 1.43;
      }
    }
  }
}

.header-menu__search-input-icon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);

  color: $color-kelly-green;

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }

  @include vp--599 {
    left: 20px;
  }
}

.header-menu__reset-bth {
  @include reset-btn;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);

  width: 40px;
  height: 40px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 18px;
    height: 2px;

    background-color: $color-black;

    transition: background-color $trans--300;
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-kelly-green;
      }
    }
  }
}

.header-menu__account-link {
  display: flex;
  align-items: center;

  color: $color-black-060;
}

.header-menu__link-text {
  margin-right: 10px;

  @include vp--599 {
    display: none;
  }
}

.header-menu__cart-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;

      .header-menu__link-icon {
        color: $color-tangerine;
      }

      .header-menu__cart-counter {
        color: $color-tangerine;
      }
    }
  }
}

.header-menu__cart-counter {
  display: block;
  position: absolute;
  top: 8px;
  right: 6px;

  min-width: 12px;
  height: 12px;
  padding: 1px 2px 0;

  font-weight: 500;
  font-size: 8px;
  line-height: 1.125;
  text-align: center;

  background-color: $color-default-white;
  border: 1px solid $color-gainsboro;
  border-radius: 6px;

  transition: color $trans--300;
}

.header-menu__link-icon {
  color: $color-kelly-green;

  transition: color $trans--300;

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }
}

.header-menu__phone-link {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  @include vp--1023 {
    position: absolute;

    visibility: hidden;
    opacity: 0;
  }
}

.header-menu__burger {
  @include reset-btn;
  position: relative;
  display: none;

  width: 40px;
  height: 40px;

  @include vp--1023 {
    display: block;
  }

  @include vp--599 {
    width: 50px;
  }
}

.header-menu__burger-line {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 40px;
  height: 6px;

  background-color: $color-kelly-green;

  &--top {
    transform: translate(-50%, calc(-50% - 12px));
  }

  &--center {
    transform: translate(-50%, -50%);
  }

  &--bottom {
    transform: translate(-50%, calc(-50% + 12px));
  }

  @include vp--599 {
    width: 30px;
    height: 4px;

    &--top {
      transform: translate(-50%, calc(-50% - 8px));
    }

    &--bottom {
      transform: translate(-50%, calc(-50% + 8px));
    }
  }
}
