.projects {
  padding: 60px 0 100px;

  @include vp--1023 {
    padding-top: 40px;
  }
}

.projects__title {
  margin: 0;
  margin-bottom: 60px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;

  .content &:not(:last-child) {
    margin-bottom: 60px;
  }

  @include vp--1023 {
    .content &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
}

.projects__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;

  .projects & {
    &:not(:last-child) {
      margin-bottom: 60px;
    }
  }

  @include vp--1023 {
    grid-template-columns: 1fr;

    .projects & {
      &:not(:last-child) {
        margin-bottom: 40px;
      }
    }
  }
}
