.account-proposal {
  display: flex;

  &--is-detailed {
    padding-top: 20px;
  }

  @include vp--1279 {
    &--is-detailed {
      display: block;
    }
  }

  @include vp--599 {
    display: block;
  }
}

.account-proposal__cover {
  flex-shrink: 0;
  width: 280px;
  min-height: 200px;
  margin-right: 20px;

  .account-proposal--is-detailed & {
    width: 480px;
    min-height: 340px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp--1279 {
    .account-proposal--is-detailed & {
      position: relative;
      width: 100%;
      min-height: unset;
      margin-right: 0;
      margin-bottom: 20px;
      padding-top: percentage(10 / 14);

      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }
    }
  }

  @include vp--767 {
    width: 240px;
  }

  @include vp--599 {
    position: relative;
    width: 100%;
    min-height: unset;
    margin-right: 0;
    margin-bottom: 20px;
    padding-top: percentage(10 / 14);

    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}

.account-proposal__content {
  flex-grow: 1;
}

.account-proposal__title {
  margin: 0;
  margin-bottom: 20px;

  color: $color-tangerine;
}

.account-proposal__timing {
  margin-bottom: 20px;
}

.account-proposal__timing-desc {
  margin: 0;

  font-weight: 500;
}

.account-proposal__timing-content {
  margin: 0;
}

.account-proposal__desc {
  margin-bottom: 10px;

  .account-proposal--is-detailed & {
    margin-bottom: 0;
  }

  p {
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &--lead {
    p {
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}

.account-proposal__link {
  font-size: 12px;
  line-height: 1.67;
  text-decoration: underline;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}
