.brand-card {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 160px;
  height: 160px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $shadow--1;

  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;

    transition: transform $trans--600;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      img {
        transform: scale(1.03);
      }
    }
  }
}
