.media {
  padding: 60px 0 100px;

  @include vp--1023 {
    padding: 40px 0 100px;
  }
}

.media__header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 60px;

  @include vp--1279 {
    max-width: 720px;
  }

  @include vp--1023 {
    margin-bottom: 40px;
  }
}

.media__title {
  margin: 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;

  color: $color-kelly-green;
}

.media__sort-select {
  flex-shrink: 0;

  margin-left: 20px;
  .custom-select {
    margin-bottom: 0;
  }
}

.media__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px 20px;

  margin-bottom: 60px;

  @include vp--1279 {
    grid-template-columns: 1fr;
  }
}

.media__item {
  display: flex;
}
