.custom-toggle {
  position: relative;

  display: flex;
  margin-bottom: 20px;

  &__error {
    position: absolute;
    left: 30px;
    top: 100%;

    font-size: 12px;
    line-height: 1.33;
    color: $color-fire-engine-red;

    opacity: 0;

    transition: opacity $trans--300;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    position: relative;

    padding-left: 30px;

    cursor: pointer;

    transition: color $trans--300;

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  label:hover,
  input:focus + label {
    color: $color-kelly-green;
  }

  &--checkbox {
    label {
      &::before {
        content: "";
        position: absolute;
        top: 2px;
        left: 2px;

        width: 16px;
        height: 16px;

        border: 1px solid $color-kelly-green;
        border-radius: 2px;

        transition: border $trans--300;
      }
    }

    input:checked + label::before {
      background-image: url("../img/svg/icon-check-light-grey.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-color: $color-kelly-green;
    }
  }

  &--radio {
    label {
      &::before {
        content: "";
        position: absolute;
        top: 2px;
        left: 0;

        width: 16px;
        height: 16px;

        border: 1px solid $color-kelly-green;
        border-radius: 50%;

        transition: border $trans--300;
      }

      &::after {
        content: "";
        position: absolute;
        top: 6px;
        left: 4px;

        width: 8px;
        height: 8px;

        background-color: $color-transparent;
        border-radius: 50%;

        transition: background-color $trans--300;
      }
    }

    input:checked + label::after {
      background-color: $color-kelly-green;
    }
  }

  input:disabled + label::before {
    opacity: 0.5;
  }

  &.is-invalid {
    label span {
      color: $color-fire-engine-red;
    }

    .custom-toggle__error {
      opacity: 1;
    }
  }

  /*&.is-valid {
    label span {
      color: $color-eucalyptus;
    }
  }*/
}
