.bread-crumbs {
  @include reset-list;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &--simple {
    margin-bottom: 50px;

    @include vp--1023 {
      margin-bottom: 30px;
    }
  }
}

.bread-crumbs__item {
  font-size: 12px;
  line-height: 1.67;

  &:not(:first-child) {
    position: relative;
    margin-left: 30px;

    &::before,
    &::after {
      position: absolute;
      content: "";
      top: 50%;
      left: -15px;

      width: 8px;
      height: 2px;

      background-color: $color-default-white;
    }

    &::before {
      transform-origin: bottom right;
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform-origin: top right;
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  .bread-crumbs--simple & {
    &:not(:first-child) {
      &::before,
      &::after {
        background-color: $color-black;
      }
    }
  }
}

.bread-crumbs__link {
  display: block;
  padding: 10px 0;
}
