*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.43;
  font-family: $font-family--roboto;
  color: $color-black;

  background-color: $color-default-white;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  //      
  text-rendering: optimizeLegibility;
  //       
  // https://transfonter.org/ -   https://prnt.sc/12rnt6g   

  @include vp--599 {
    font-size: 12px;
    line-height: 1.5;
  }
}

h1,
.h1 {
  font-weight: 400;
  font-size: 24px;
  line-height: 1.33;
}

h2,
.h2 {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
}

h3,
.h3 {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
}

h4,
.h4 {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
}

h5,
.h5 {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.43;
}

h6,
.h6 {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.33;
  color: $color-black-040;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// chrome autofill background removal
//         \ -
//          
// rgba  ,    hex  
//      box-shadow    -
//     `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;

  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
