.account__header {
  padding: 40px 0;

  color: $color-default-white;
  background-color: $color-kelly-green;

  @include vp--599 {
    padding: 20px 0;
  }
}

.account__title {
  margin: 0;

  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;

  @include vp--599 {
    font-size: 16px;
    line-height: 1.25;
  }
}

.account__container {
  display: flex;

  padding-top: 40px;
  padding-bottom: 100px;

  @include vp--1023 {
    display: block;

    padding-top: 0;
  }

  @include vp--599 {
    padding-bottom: 100px;
  }
}

.account__menu {
  flex: 0 0 280px;

  margin-right: 20px;

  @include vp--1023 {
    margin-right: 0;
  }
}

.account__content {
  flex-grow: 1;
  width: calc(100% - 300px);

  @include vp--1023 {
    width: 100%;
    padding-top: 40px;
  }

  @include vp--599 {
    padding-top: 20px;
  }
}

.account__content-title {
  margin: 0;
  margin-bottom: 20px;
  padding-bottom: 20px;

  border-bottom: 1px solid $color-kelly-green;

  &--double-indent {
    margin-bottom: 40px;
  }
}

.account__personal {
  @include reset-list;

  margin-bottom: 20px;
}

.account__personal-item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.account__edit-link {
  text-decoration-line: underline;
  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.account__controls {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  @include vp--599 {
    &--alt-mobile-view {
      flex-direction: column;
    }
  }
}

.account__control-select {
  &:not(:last-child) {
    margin-right: 30px;
  }

  &:last-child {
    margin-left: auto;
  }

  @include vp--599 {
    &:last-child {
      margin-left: 0;
    }
  }
}

.account__arrears {
  margin-bottom: 20px;
  padding: 20px;

  font-size: 16px;
  line-height: 20px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--15;

}

.account__arrears-value {
  display: inline-block;
  margin-left: 10px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;

  @include vp--599 {
    font-size: 18px;
    line-height: 1.1;
  }
}

.account__orders {
  @include reset-list;
}

.account__order {
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  @include vp--599 {
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
}

.account__proposals {
  @include reset-list;

  margin-bottom: 40px;
}

.account__proposal {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.account__submenu {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  margin:-10px 0 30px;
}

.account__submenu-list {
  @include reset-list;
  display: flex;

  width: fit-content;
  min-width: 100%;
}

.account__submenu-item {
  &:not(:last-child) {
    margin-right: 40px;
  }
}

.account__submenu-link {
  display: block;

  width: max-content;
  padding: 10px 0;

  font-weight: 500;
  color: $color-black-060;

  &--active {
    color: $color-tangerine;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-kelly-green;
    }
  }
}

.account__content-desc {
  margin: 0;
  margin-bottom: 20px;

  font-size: 14px;
  line-height: 1.43;
}

.account__period {
  position: relative;

  width: 240px;

  &::after {
    position: absolute;
    content: "";
    top: 50%;
    right: 17px;
    transform: translateY(-50%);

    width: 10px;
    height: 5px;
    margin-left: auto;

    background-color: $color-kelly-green;

    transition: transform $trans--300;

    clip-path: polygon(0 0, 100% 0, 50% 100%);
  }

  label {
    input {
      padding: 4px 19px;
      padding-right: 39px;
    }
  }
}

.account__control-submit-btn {
  margin-left: auto;
  margin-bottom: 20px;

  @include vp--599 {
    margin-left: 0;
  }
}

.account__report {
  padding: 20px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--13;
}

.account__report-title {
  margin: 0;
  padding-bottom: 20px;

  border-bottom: 1px solid $color-gainsboro;
}

.account__report-content {
  min-height: 300px;
  margin-top: 20px;
}

.account__documents {
  @include reset-list;
}

.account__document {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  &:not(:last-child) {
    margin-bottom: 15px;
  }

  @include vp--599 {
    display: block;
  }

  .account__documents--simple & {
    display: block;
    margin-bottom: 20px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.account__document-name {
  margin: 0;
  margin-right: 40px;
  margin-bottom: 5px;

  font-size: 14px;
  line-height: 1.43;

  .account__documents--simple & {
    margin-right: 0;
    margin-bottom: 0;

    @include vp--599 {
      font-size: 12px;
      line-height: 1.67;
    }
  }
}

.account__document-link {
  flex-shrink: 0;

  margin-bottom: 5px;

  font-size: 14px;
  line-height: 1.43;
  text-decoration: underline;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }

  @include vp--599 {
    display: block;
    width: fit-content;
  }

  .account__documents--simple & {
    margin-bottom: 0;

    @include vp--599 {
      font-size: 12px;
      line-height: 1.67;
    }
  }
}

.account__refund-link {
  margin-bottom: 60px;

  @include vp--767 {
    margin-bottom: 40px;
  }
}

.account__refunds-title {
  margin: 0;
  margin-bottom: 20px;
}

.account__refunds {
  @include reset-list;
}

.account__refund {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.account__refunds-title {
  margin: 0;
  margin-bottom: 20px;

  color: $color-kelly-green;
}

.account__refund-option-cell {
  width: 80px;
  margin-left: 20px;
  margin-bottom: 10px;

  font-size: 14px;
  line-height: 1.43;

  &:last-child {
    margin-right: 40px;
  }

  &--order-id {
    width: 160px;

    font-weight: 500;
  }

  &--order-cost {
    width: 120px;

    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
  }
}

.account__refund-products-title {
  margin: 0;
  margin-bottom: 20px;
}

.account__refund-product {
  label {
    display: flex;
  }

  @include vp--599 {
    label {
      flex-wrap: wrap;
    }
  }
}

.account__refund-product-cell {
  font-size: 14px;
  line-height: 1.43;

  &--product-title {
    flex-shrink: 1;
    max-width: 540px;

    strong {
      font-weight: 500;
    }
  }

  &--product-quantity {
    flex-shrink: 0;
    width: 80px;
    margin-left: 40px;
  }

  &--product-cost {
    flex-shrink: 0;
    width: 100px;
    margin-left: 20px;

    font-weight: 500;
    font-size: 20px;
    line-height: 1;
  }

  @include vp--1023 {
    &--product-quantity {
      margin-left: 30px;
    }

    &--product-cost {
      margin-left: 15px;
    }
  }

  @include vp--599 {
    &--product-title {
      flex-shrink: unset;
      max-width: unset;
      width: 100%;
      margin-bottom: 10px;
    }
    &--product-quantity {
      margin-left: 0;
    }
  }
}

.account__delivery-addresses {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(auto-fit, 280px);
  gap: 20px;

  margin-bottom: 40px;

  @include vp--767 {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

.account__delivery-calculator-city {
  position: relative;
}

.account__delivery-calculator-search-icon{
  position: absolute;
  top: calc(30px / 2 + 50%);
  left: 20px;
  transform: translateY(-50%);

  color: $color-kelly-green;

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }
}

.account__delivery-calculator {
  max-width: 680px;
}

.account__delivery-calculator-city-input{
  label {
    flex-direction: column;
    align-items: flex-start;

    input {
      width: 100%;
      padding-left: 49px;
      padding-right: 49px;
    }
  }

  .custom-input__label {
    width: 100%;
    margin-bottom: 10px;
    margin-right: 0;

    font-weight: 500;
  }
}

.account__delivery-calculator-reset-bth {
  @include reset-btn;
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translateY(calc(30px / 2 - 50%));

  width: 40px;
  height: 40px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 18px;
    height: 2px;

    background-color: $color-black;

    transition: background-color $trans--300;
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-kelly-green;
      }
    }
  }
}

.account__delivery-calculator-input {
  max-width: 380px;

  label {
    input {
      width: 100%;
    }
  }

  .custom-input__label {
    flex-shrink: 0;

    font-weight: 500;
  }

  &--wide {
    label {
      width: 100%;
      flex-direction: column;
      align-items: flex-start;
    }

    .custom-input__label {
      margin-bottom: 10px;
      margin-right: 0;
    }
  }
}

.account__delivery-calculator-inputs {
  display: flex;
  justify-content: space-between;

  max-width: 380px;

  .account__delivery-calculator-input {
    width: calc(50% - 20px / 2);
  }
}

.account__delivery-calculator-carriers-title {
  margin: 0;
  margin-bottom: 10px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
}

.account__delivery-calculator-carriers {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  margin: 0 -10px 30px;
}

.account__delivery-calculator-carrier {
  width: 50%;
  max-width: 180px;

  padding: 0 10px;
  padding-top: 0;

  label {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 60px;
    margin-bottom: 10px;

    border: 1px solid $color-whisper-2;

    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
  }

  input:checked + label {
    border-color: $color-kelly-green;
  }

  label:hover,
  input:focus + label {
    border-color: $color-kelly-green;
  }
}

.account__delivery-calculator-carrier-name {
  margin: 0;

  font-size: 14px;
  line-height: 1.29;

  @include vp--599 {
    font-size: 12px;
    line-height: 1.5;
  }
}

.account__delivery-calculator-summary {
  margin: 0;
  margin-top: 40px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
}

.account__delivery-calculator-summary-value {
  font-weight: 400;
}

.account__pricelists {
  @include reset-list;

  padding: 25px 20px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--13;

  @include vp--599 {
    padding-bottom: 30px;
  }
}

.account__pricelist {
  display: flex;
  align-items: flex-end;

  font-size: 14px;
  line-height: 1.43;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  @include vp--599 {
    display: block;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}

.account__pricelist-data {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include vp--767 {
    display: block;
  }
}

.account__pricelist-name {
  margin: 0;
  margin-right: 40px;
  padding: 5px 0;

  font-weight: 500;
}

.account__pricelist-load-date {
  margin: 0;
  margin-right: 20px;
  padding: 5px 0;
}

.account__pricelist-links {
  @include reset-list;

  flex-shrink: 0;
  display: flex;
  flex-wrap: wrap;

  max-width: 250px;
  padding: 5px 0;

  @include vp--599 {
    max-width: unset;
    margin-top: 10px;
  }
}

.account__pricelist-link-item {
  margin-left: 20px;

  @include vp--599 {
    margin-left: 0;
    margin-right: 20px;
  }
}

.account__pricelist-link {
  color: $color-kelly-green;
  text-decoration: underline;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.account__documents {
  @include reset-list;
}

.account__document {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  &:not(:last-child) {
    margin-bottom: 15px;
  }

  @include vp--599 {
    display: block;
  }
}

.account__document-name {
  margin: 0;
  margin-right: 40px;
  margin-bottom: 5px;

  font-size: 14px;
  line-height: 1.43;
}

.account__document-link {
  flex-shrink: 0;

  margin-bottom: 5px;

  font-size: 14px;
  line-height: 1.43;
  text-decoration: underline;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }

  @include vp--599 {
    display: block;
    width: fit-content;
  }
}

ul.account__access-list {
  @include reset-list;

  margin-bottom: 20px;
}

.account__access-item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.account__access-add-user-btn {
  margin-left: auto;

  @include vp--599 {
    margin-right: auto;
  }
}

.account__dynamics-list {
  @include reset-list;

  margin-bottom: 40px;
}

.account__dynamics-item {
  &:not(:first-child) {
    margin-top: -1px;
  }
}

.account__dynamics-btn {
  @include reset-btn;

  position: relative;

  width: 100%;
  padding: 19px;
  padding-right: 59px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
  text-align: left;

  color: $color-kelly-green;
  background: $color-default-white;
  border: 1px solid $color-white-smoke;

  transition: color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-kelly-green;
    }
  }
}

.account__dynamics-btn-icon {
  @include reset-btn;
  position: absolute;
  top: 20px;
  right: 20px;

  width: 20px;
  height: 20px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: $color-kelly-green;

    transition: transform $trans--300;
  }

  &::before {
    width: 18px;
    height: 2px;
  }

  &::after {
    width: 2px;
    height: 18px;
  }

  .account__dynamics-item.is-active & {
    &::before,
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}

.account__dynamics-data-wrapper{
  padding: 20px;

  @include vp--767 {
    padding: 20px 0;
  }
}
