.indicators {
  padding: 60px 0;

  color: $color-default-white;
  background-color: $color-kelly-green;
}

.indicators__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
}

.indicators__achievements {
  @include reset-list;
  display: grid;
  grid-template-columns: repeat(12, 80px);
  grid-auto-rows: 80px;
  grid-auto-flow: dense;
  gap: 20px;

  @include vp--1279 {
    grid-template-columns: repeat(7, 80px);

    width: 680px;
    margin: 0 auto;
  }

  @include vp--767 {
    grid-template-columns: repeat(4, 58px);
    grid-auto-rows: 58px 58px 58px 58px 70px 58px 58px 70px 70px 70px;
    gap: 16px;

    width: 280px;
  }
}

.indicators__achievement {
  display: flex;
  justify-content: center;
  align-items: flex-end;

  border-radius: 10px;
  box-shadow: $complex-shadow--4;

  &:nth-child(7n + 1) {
    flex-direction: column;
    align-items: center;
    grid-column: 3 span;
    grid-row: 3 span;

    padding: 24px;
  }

  &:nth-child(7n + 2) {
    grid-column: 4 span;
    grid-row: 1 span;

    padding: 8px;
  }

  &:nth-child(7n + 3),
  &:nth-child(7n + 4) {
    flex-direction: column;
    align-items: center;
    grid-row: 2 span;

    padding: 16px;
  }

  &:nth-child(7n + 3) {
    grid-column: 4 / 2 span;
  }

  &:nth-child(7n + 4) {
    grid-column: 6 / 2 span;
  }

  &:nth-child(7n + 5),
  &:nth-child(7n + 6),
  &:nth-child(7n + 7) {
    grid-column: 5 span;
    grid-row: 1 span;

    padding: 8px;
  }

  &:nth-child(7n + 6) {
    flex-direction: row-reverse;
  }

  @include vp--1279 {
    &:nth-child(7n + 5),
    &:nth-child(7n + 6),
    &:nth-child(7n + 7) {
      grid-column: 7 span;
    }
  }

  @include vp--767 {
    &:nth-child(7n + 1) {
      grid-column: 4 span;
      grid-row: 4 span;

      padding: 15px;
    }

    &:nth-child(7n + 3),
    &:nth-child(7n + 4) {
      grid-column: 2 span;

      padding: 10px;
    }

    &:nth-child(7n + 2),
    &:nth-child(7n + 5),
    &:nth-child(7n + 6),
    &:nth-child(7n + 7) {
      grid-column: 4 span;

      padding: 5px;
    }
  }
}

.indicators__achievement-value {
  flex-shrink: 0;

  margin-right: 10px;

  font-weight: 700;
  font-size: 64px;
  line-height: 1;

  .indicators__achievement:nth-child(7n + 1) & {
    margin-right: 0;

    font-size: 144px;
  }

  .indicators__achievement:nth-child(7n + 3) &,
  .indicators__achievement:nth-child(7n + 4) & {
    font-size: 96px;
  }

  .indicators__achievement:nth-child(7n + 6) & {
    margin-left: 10px;
    margin-right: 0;
  }

  @include vp--767 {
    font-size: 48px;
    line-height: 1.25;

    .indicators__achievement:nth-child(7n + 3) &,
    .indicators__achievement:nth-child(7n + 4) & {
      font-size: 72px;
    }
  }
}

.indicators__achievement-desc {
  margin-bottom: 8px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.29;
  text-transform: uppercase;

  .indicators__achievement:nth-child(7n + 1) & {
    margin-bottom: 0;

    font-size: 24px;
    line-height: 1;
    text-align: center;
  }

  .indicators__achievement:nth-child(7n + 3) &,
  .indicators__achievement:nth-child(7n + 4) & {
    text-align: center;
  }

  .indicators__achievement:nth-child(7n + 5) &,
  .indicators__achievement:nth-child(7n + 7) & {
    text-align: right;
  }

  @include vp--767 {
    font-size: 10px;
    line-height: 1.4;
  }
}
