.account-menu {
  margin: -10px 0;

  @include vp--1023 {
    position: relative;
    z-index: 11;

    margin: 0 -30px;
    padding: 30px 30px 10px;

    background-color: $color-white-smoke;
  }

  @include vp--599 {
    margin: 0 -20px;
    padding: 10px 20px 10px;

    background-color: $color-white-smoke;
  }
}

.account-menu__overlay {
  .account-menu.is-shown & {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
  }
}

.account-menu__list {
  @include reset-list;

  @include vp--1023 {
    overflow: hidden;
    visibility: hidden;
    opacity: 0;

    position: absolute;
    top: 100%;
    left: 0;
    z-index: 11;

    width: 280px;
    padding: 10px 30px;

    background-color: $color-default-white;
    border-radius: 0px 0px 5px 5px;
    box-shadow: $shadow--5;

    .account-menu.is-hide & {
      transition:
        overflow $trans--300,
        visibility $trans--300,
        opacity $trans--300;
    }

    .account-menu.is-shown & {
    overflow: visible;
    visibility: visible;
    opacity: 1;

    transition:
      overflow $trans--300,
      visibility $trans--300,
      opacity $trans--300;
    }
  }

  @include vp--599 {
    padding: 10px 20px;
  }
}

.account-menu__link {
  display: block;

  width: fit-content;
  padding: 10px 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;

  &--active {
    color: $color-kelly-green;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.account-menu__sublist {
  @include reset-list;

  margin-top: 10px;

  .account-menu__item:not(:last-child) & {
    margin-bottom: 10px;
  }

  @include vp--1023 {
    margin-top: 5px;

    .account-menu__item:not(:last-child) & {
      margin-bottom: 5px;
    }
  }
}

.account-menu__sublink {
  display: block;

  width: fit-content;

  font-weight: 300;
  font-size: 14px;
  line-height: 1.43;

  &--active {
    color: $color-tangerine;
  }

  @include vp--1023 {
    padding: 5px 0;
  }
}

.account-menu__toggle {
  @include reset-btn;

  position: relative;
  z-index: 1;

  display: none;
  align-items: center;

  padding: 10px 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;

  color: $color-kelly-green;

  @include vp--1023 {
    display: flex;
  }
}

.account-menu__toggle-icon {
  position: relative;
  width: 20px;
  height: 20px;
  margin-left: 20px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;

    width: 8px;
    height: 2px;
    background-color: $color-kelly-green;
  }

  &::before {
    right: 50%;
    transform-origin: right bottom;
    transform: translateY(1px) rotate(45deg);
  }

  &::after {
    left: 50%;
    transform-origin: left bottom;
    transform: translateY(1px) rotate(-45deg);
  }
}
