.partners {
  padding: 60px 0;

  &:last-of-type {
    padding-bottom: 100px;
  }

  @include vp--1023 {
    padding: 40px 0;
  }
}

.partners__title {
  margin: 0;
  margin-bottom: 60px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;

  color: $color-kelly-green;

  @include vp--1023 {
    margin-bottom: 40px;
  }
}

.partners__list {
  @include reset-list;
  display: grid;
  grid-template-columns: repeat(6, 180px);
  gap: 20px;

  @include vp--1279 {
    grid-template-columns: repeat(4, 180px);
  }

  @include vp--1023 {
    grid-template-columns: repeat(3, 180px);
  }

  @include vp--767 {
    grid-template-columns: repeat(3, 1fr);

    max-width: 580px;
  }

  @include vp--599 {
    grid-template-columns: repeat(2, 1fr);

    max-width: 380px;
    width: 100%;
  }
}
