.modal-add-address__search-input {
  position: relative;

  label {
    input {
      padding: 9px 49px;
    }
  }
}

.modal-add-address__form-city {
  position: relative;

  label {
    input {
      padding-left: 49px;
      padding-right: 49px;
    }
  }

  @include vp--599 {
    label {
      input {
        padding-left: 39px;
        padding-right: 39px;
      }
    }
  }
}

.modal-add-address__search-icon {
  position: absolute;
  top: calc(50%);
  left: 180px;
  transform: translateY(-50%);

  color: $color-kelly-green;

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }

  @include vp--599 {
    left: 10px;
    transform: translateY(calc(30px / 2 - 50%));
  }
}

.modal-add-address__form-reset-bth {
  @include reset-btn;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);

  width: 40px;
  height: 40px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 18px;
    height: 2px;

    background-color: $color-black;

    transition: background-color $trans--300;
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-kelly-green;
      }
    }
  }

  @include vp--599 {
    right: 0;
    transform: translateY(calc(30px / 2 - 50%));
  }
}

.modal-add-address__inputs {
  display: flex;
  justify-content: space-between;

  margin-left: 160px;

  @include vp--599 {
    margin-left: 0;
  }
}

.modal-add-address__input {
  .modal-add-address__inputs & {
    label {
      input {
        width: 130px;
      }
    }
  }

  .custom-input__label {
    width: 140px;
  }

  @include vp--599 {
    .modal-add-address__inputs & {
      width: calc(50% - 10px / 2);

      label {
        input {
          width: 100%;
        }
      }
    }
  }
}

.modal-add-address__submit-btn {
  margin-top: 40px;
}
