.partnership-benefits {
  padding: 60px 0;

  color: $color-default-white;
  background-color: $color-kelly-green;

  @include vp--1023 {
    padding: 40px 0;
  }
}

.partnership-benefits__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-transform: uppercase;
}

.partnership-benefits__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;

  @include vp--1023 {
    grid-template-columns: 100%;
  }
}

.partnership-benefits__item {
  display: flex;
  align-items: center;

  @include vp--1023 {
    &:nth-child(2n) {
      order: 2;
    }
  }
}

.partnership-benefits__icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 80px;
  height: 80px;
  padding: 10px;

  background-image: $green-135;
  border-radius: 50%;
  box-shadow: $complex-shadow--9;

  @include vp--1023 {
    align-self: flex-start;
  }
}

.partnership-benefits__desc {
  margin: 0;
  margin-left: 20px;

  font-weight: 500;
}
