.catalog {
  padding: 30px 0 100px;

  @include vp--1023 {
    padding-top: 10px;
  }
}

.catalog__categories {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;

  margin-top: 30px;

  @include vp--1023 {
    grid-template-columns: repeat(3, 1fr);
  }

  @include vp--599{
    grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
  }
}

.catalog__category-title {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;

  color: $color-tangerine;
}

.catalog__content {
  display: flex;
  align-items: flex-start;

  @include vp--1023 {
    flex-direction: column;
    align-items: stretch;
  }
}

.catalog__filter {
  flex: 0 0 180px;

  margin-right: 120px;

  @include vp--1023 {
    position: relative;
    z-index: 11;

    flex: 0 0 auto;
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.catalog__filter-btn {
  @include reset-btn;

  display: none;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  margin-bottom: 20px;
  padding: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;

  color: $color-kelly-green;

  background-color: $color-white-smoke;

  @include vp--1023 {
    display: flex;
    margin-bottom: 0;
  }
}

.catalog__filter-btn-icon {
  position: relative;
  width: 20px;
  height: 20px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;

    width: 8px;
    height: 2px;
    background-color: $color-kelly-green;
  }

  &::before {
    right: 50%;
    transform-origin: right bottom;
    transform: translateY(1px) rotate(45deg);
  }

  &::after {
    left: 50%;
    transform-origin: left bottom;
    transform: translateY(1px) rotate(-45deg);
  }
}

.catalog__filter-overlay {
  .catalog__filter.is-shown & {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.catalog__filter-content {
  width: 100%;

  @include vp--1023 {
    overflow: hidden;
    visibility: hidden;
    opacity: 0;

    position: absolute;
    top: 100%;
    left: 0;
    z-index: 11;

    width: 280px;
    padding: 20px;

    background-color: $color-default-white;
    border-radius: 0px 0px 5px 5px;
    box-shadow: $shadow--5;

    .catalog__filter.is-hide & {
      transition:
        overflow $trans--300,
        visibility $trans--300,
        opacity $trans--300;
    }

    .catalog__filter.is-shown & {
    overflow: visible;
    visibility: visible;
    opacity: 1;

    transition:
      overflow $trans--300,
      visibility $trans--300,
      opacity $trans--300;
    }
  }
}

.catalog__wrapper {
  flex: 1 1 auto;
}

.catalog__controls {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  margin-bottom: 30px;
}

.catalog__sort-select {
  margin-bottom: 0;
}

.catalog__view-btns {
  @include reset-list;

  display: flex;

  margin: -5px;
  margin-left: 15px;
}

.catalog__view-btn {
  @include reset-btn;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 30px;
  height: 30px;

  color: black;

  transition: color $trans--300;

  &:disabled {
    color: $color-kelly-green;
  }

  svg {
    width: 20px;
    height: 20px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover:not(:disabled),
    &:focus {
      color: $color-kelly-green;
    }
  }

  @include vp--767 {
    &[data-view="small-tiles"] {
      display: none;
    }
  }
}

.catalog__header {
  display: none;

  padding: 10px;

  font-weight: 500;
  color: $color-default-white;
  background-color: $color-kelly-green;

  .catalog__content--table & {
    display: flex;
  }

  @include vp--767 {
    .catalog__content--table & {
      display: none;
    }
  }
}

.catalog__header-title {
  flex: 0 3 380px;

  padding: 10px;
}

.catalog__header-price {
  flex: 0 2 200px;

  padding: 10px;
}

.catalog__header-quantity {
  flex: 0 0 100px;

  padding: 10px;
}

.catalog__header-btns {
  flex: 0 0 90px;

  margin-left: auto;
}

.catalog__products {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px 20px;

  margin-bottom: 60px;

  .catalog__content--large-tiles & {
    grid-template-columns: repeat(3, 1fr);
  }

  .catalog__content--small-tiles & {
    grid-template-columns: repeat(4, 1fr);
  }

  .catalog__content--table & {
    display: block;
  }

  @include vp--1279 {
    grid-template-columns: repeat(2, 1fr);

    .catalog__content--large-tiles & {
      grid-template-columns: repeat(2, 1fr);
    }

    .catalog__content--small-tiles & {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  @include vp--767 {
    grid-template-columns: repeat(1, 1fr);

    .catalog__content--large-tiles & {
      grid-template-columns: repeat(1, 1fr);
    }

    .catalog__content--small-tiles & {
      grid-template-columns: repeat(1, 1fr);
    }

    .catalog__content--table & {
      border-top: 1px solid $color-gainsboro;
    }
  }
}

.catalog__product {
  display: flex;
  flex-direction: column;

  .catalog__content--table & {
    border-bottom: 1px solid $color-gainsboro;
  }
}
