.social {
  @include reset-list;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;

  margin: -10px;

  @include vp--1023 {
    justify-content: flex-start;
  }
}

.social__item {
  overflow: hidden;

  margin: 10px;

  background-color: $color-default-white;
  border-radius: 50%;
}

.social__link {
  display: flex;

  width: 40px;
  height: 40px;

  color: $color-kelly-green;

  svg {
    display: block;
    width: 40px;
    height: 40px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &[href]:hover,
    &:focus {
      color: $color-tangerine;
    }
  }
}
