.modal-product-added-to-cart__title {
  margin: 0;
  margin-bottom: 20px;
  padding-right: 30px;
}

.modal-product-added-to-cart__wrapper {
  display: flex;

  margin-bottom: 20px;

  @include vp--599 {
    flex-direction: column;
  }
}

.modal-product-added-to-cart__product-img {
  flex-shrink: 0;

  width: 240px;
  height: 240px;
  margin-right: 20px;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  @include vp--599 {
    position: relative;

    width: 100%;
    height: 100%;
    margin-right: 0;
    margin-bottom: 20px;
    padding-top: 100%;

    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}

.modal-product-added-to-cart__product-data {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;

  width: 240px;
  padding: 20px 0;

  @include vp--599 {
    width: 100%;
    padding: 0;
  }
}

.modal-product-added-to-cart__product-title {
  margin: 0;
  margin-bottom: 20px;
}

.modal-product-added-to-cart__product-counter {
  margin: auto 0 20px;
}

.modal-product-added-to-cart__product-price {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;
}

.modal-product-added-to-cart__actions {
  display: flex;

  @include vp--599 {
    flex-direction: column;
  }
}

.modal-product-added-to-cart__continue-shopping-btn {
  margin-right: 20px;

  @include vp--599 {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
