.exhibition {
  padding: 30px 0 100px;

  @include vp--1023 {
    padding-top: 10px;
  }

  h1,
  h2,
  .h2 {
    color: $color-tangerine;

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
}

.exhibition__subtitle {
  margin: 0;
  margin-bottom: 20px;

  color: $color-kelly-green;
}

.exhibition__desc {
  margin: 0;
  margin-bottom: 60px;

  @include vp--1023 {
    margin-bottom: 40px;
  }
}
