.modal-submit-application__title {
  max-width: 380px;
  margin: 0 auto 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  color: $color-kelly-green;
}

.modal-submit-application__desc {
  max-width: 380px;
  margin: 0 auto 40px;

  font-size: 16px;
  line-height: 1.5;
  text-align: center;
}

.modal-submit-application__input {
  &--size-s {
    input {
      width: 100px;
    }
  }

  &--size-m{
    input {
      width: 130px;
    }
  }

  &--size-l {
    input {
      width: 190px;
    }
  }

  .modal-submit-application__address & {
    margin-right: 10px;
  }

  @include vp--599 {
    .modal-submit-application__address & {
      &--size-s {
        width: 100px;
      }
    }
  }
}

.modal-submit-application__address {
  visibility: hidden;
  overflow: hidden;
  opacity: 0;

  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;

  max-height: 0;
  margin-right: -10px;

  transition:
    visibility $trans--300,
    overflow $trans--300,
    opacity $trans--300,
    max-height $trans--300;

  .modal-submit-application--with-address & {
    visibility: visible;
    overflow: visible;
    opacity: 1;
  }
}
