@mixin vp--1279 {
  @media (max-width: $vp--1280 - 1) {
    @content;
  }
}

@mixin vp--1023 {
  @media (max-width: $vp--1024 - 1) {
    @content;
  }
}

@mixin vp--767 {
  @media (max-width: $vp--768 - 1) {
    @content;
  }
}

@mixin vp--599 {
  @media (max-width: $vp--600 - 1) {
    @content;
  }
}

@mixin vp--475 {
  @media (max-width: $vp--476 - 1) {
