@mixin reset-btn {
  padding: 0;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  background-color: $color-transparent;
  border: none;
  outline: none;

  &:not(:disabled) {
    cursor: pointer;
  }
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
