.partner-card {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  padding-bottom: 100%;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--8;

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: contain;

    width: 100%;
    height: 100%;

    transition: transform $trans--600;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      img {
        transform: scale(1.03);
      }
    }
  }
}
