.modal-message__wrapper {
  padding: 15px 0;
}

.modal-message__icon {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 0 auto 10px;

  border-radius: 50%;

  &--positive {
    background-color: $color-kelly-green;
    background-image: url("../img/svg/icon-check-light-grey.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &--negative {
    background-color: $color-fire-engine-red;
    border-radius: 50%;

    &::before,
    &::after {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;

      width: 12px;
      height: 2px;

      background-color: $color-default-white;
    }

    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}

.modal-message__title {
  margin: 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.modal-message__details {
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  p {
    margin: 0;

    text-align: center;
  }
}

.modal-message__message {
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  p {
    margin: 0;

    font-size: 12px;
    line-height: 1.67;
    text-align: center;
  }
}

.modal-message__desc {
  margin: 0;

  font-weight: 500;
  text-align: center;
}
