.modal-authorization__title {
  margin: 0;
  margin-bottom: 20px;
}

.modal-authorization__input {
  label {
    span {
      display: none;
    }
  }
}

.modal-authorization__btn {
  margin-bottom: 20px;
}

.modal-authorization__link {
  display: block;

  width: fit-content;
  margin: 0 auto 20px;

  text-align: center;
  text-decoration: underline;
}

.modal-authorization__footer {
  margin: -20px;
  margin-top: 0;
  padding: 20px;

  background-color: $color-white-smoke;
}
