.registration__tabs-wrapper {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  padding: 20px 0;

  @include vp--767 {
    padding: 0;
  }
}

.registration__tabs {
  @include reset-list;

  display: flex;

  @include vp--767 {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      left: 0;
      right: 0;
      bottom: 1px;

      width: 100%;
      height: 1px;

      background-color: $color-whisper;
    }
  }
}

.registration__tab {
  &:not(:last-child) {
    margin-right: 80px;
  }

  &--previously-collaborated {
    display: none;
  }

  .registration--previously-collaborated & {
    &--previously-collaborated {
      display: list-item;
    }

    &--previously-not-collaborated {
      display: none;
    }
  }
}

.registration__tab-btn {
  @include reset-btn;

  width: max-content;

  font-weight: normal;
  font-size: 16px;
  line-height: 1.25;

  color: $color-black-060;

  transition: color $trans--300;

  &.is-active {
    cursor: auto;
    color: $color-tangerine;
  }

  @include vp--767 {
    position: relative;
    padding: 20px 0;

    &::before {
      position: absolute;
      content: "";
      left: 0;
      right: 0;
      bottom: -0;

      height: 4px;

      background-color: transparent;
      border-radius: 2px;

      transition: background-color $trans--300;
    }

    &.is-active {
      &::before {
        background-color: $color-tangerine;
      }
    }
  }

  @include vp--599 {
    font-size: 14px;
    line-height: 1.43;
  }
}

.registration__step {
  &--previously-collaborated {
    display: none;
  }

  .registration--previously-collaborated & {
    &--previously-collaborated {
      display: list-item;
    }

    &--previously-not-collaborated {
      display: none;
    }
  }
}

.registration__radio-group {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  padding-left: 20px;

  @include vp--1023 {
    flex-direction: column;
  }

  @include vp--599 {
    padding-left: 0;
  }
}

.registration__radio-wrapper {
  max-width: 400px;
  margin-bottom: 60px;

  &:not(:last-child) {
    margin-right: 100px;
  }

  @include vp--1023 {
    &:not(:last-child) {
      margin-right: 0;
    }
  }
}

.registration__radio-btn {
  &:last-child {
    margin-bottom: 0;
  }

  label {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
  }

  @include vp--599 {
    label {
      font-size: 14px;
      line-height: 1.43;
    }
  }
}

.registration__radio-desc {
  margin: 0;
  padding-left: 30px;
}

.registration__step-btns {
  display: flex;
  flex-wrap: wrap;

  @include vp--599 {
    flex-direction: column;
  }
}
.registration__step-btn {
  &--previously-collaborated {
    display: none;
  }

  .registration--previously-collaborated & {
    &--previously-collaborated {
      display: list-item;
    }

    &--previously-not-collaborated {
      display: none;
    }
  }
}

.registration__step-prev-btn {
  margin-right: 20px;

  transition:
    color $trans--300,
    border-color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
      border-color: $color-tangerine;

      transition:
        color $trans--300,
        border-color $trans--300;
    }
  }

  @include vp--599 {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.registration__contacts-title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
}

.registration_inputs {
  width: 440px;
  margin-left: 65px;

  @include vp--599 {
    width: 240px;
    margin-left: 40px;
  }
}

.registration__input {
  &--size-s {
    input {
      width: 100px;
    }
  }

  &--size-m{
    input {
      width: 130px;
    }
  }

  &--size-l {
    input {
      width: 190px;
    }
  }

  .registration__address & {
    margin-right: 10px;
  }

  @include vp--599 {
    .registration__address & {
      &--size-s {
        width: 100px;
      }
    }
  }
}

.registration__address {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;

  margin-right: -10px;
}

.registration__agreement {
  margin-bottom: 40px;

  @include vp--599 {
    width: 280px;
    margin-bottom: 20px;
    padding-left: 40px;
  }
}
