.cart-product-counter {
  overflow: hidden;
  position: relative;
  flex: 0 0 90px;

  min-width: unset;

  border-radius: 20px;

  input {
    width: 100%;
    padding: 10px 30px;

    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
    text-align: center;
    color: $color-default-white;

    background-color: $color-kelly-green;
    border: none;
    border-radius: 20px;
    outline: none;

    transition: background-color $trans--300;

    &:hover,
    &:focus {
      background-color: $color-tangerine;
    }
  }

  &--alt {
    flex: unset;
    width: 160px;

    border-radius: 0;

    input {
      padding: 9px 29px;

      font-weight: 400;
      color: $color-black;

      background-color: $color-default-white;
      border: 1px solid $color-whisper;
      border-radius: 5px;

      transition: border-color $trans--300;

      &:hover,
      &:focus {
        background-color: $color-default-white;
        border-color: $color-kelly-green;
      }
    }
  }

  @include vp--599 {
    &--alt {
      width: 120px;

      input {
        padding: 4px 24px;
      }
    }
  }
}

.cart-product-counter__quantity-btn {
  @include reset-btn;

  position: absolute;
  content: "";
  top: 0;

  width: 24px;
  height: 40px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: $color-default-white;

    transition: background-color $trans--300;
  }

  &::before {
    width: 14px;
    height: 2px;
  }

  &--decrement {
    left: 7px;
  }

  &--increment {
    right: 7px;

    &::after {
      width: 2px;
      height: 14px;
    }
  }

  &:disabled::before,
  &:disabled::after {
    background-color: $color-silver;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:not(:disabled):hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-tangerine;
      }
    }
  }

  .cart-product-counter--alt & {
    width: 40px;
    height: 40px;

    &::before,
    &::after {
      background-color: $color-kelly-green;
    }

    &--decrement {
      left: 10px;
    }

    &--increment {
      right: 10px;
    }
  }

  @include vp--599 {
    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        &::before,
        &::after {
          background-color: $color-tangerine;
        }
      }
    }

    .cart-product-counter--alt & {
      width: 30px;
      height: 30px;

      &::before,
      &::after {
        background-color: $color-kelly-green;
      }

      &::before {
        width: 12px;
      }

      &--decrement {
        left: 8px;
      }

      &--increment {
        right: 8px;

        &::after {
          height: 12px;
        }
      }
    }
  }
}
