.about {
  position: relative;

  min-height: 800px;
  padding: 155px 0;

  color: $color-default-white;

  @include vp--1279 {
    min-height: 620px;
    padding: 100px 0;
  }

  @include vp--767 {
    display: flex;
    align-items: center;

    min-height: calc(100vh - 80px);
    /* stylelint-disable-next-line */
    min-height: calc(100 * var(--vh, 1vh) - 80px);
    padding: 40px 0;
  }

  @include vp--599 {
    min-height: calc(100vh - 70px);
    /* stylelint-disable-next-line */
    min-height: calc(100 * var(--vh, 1vh) - 70px);
  }
}

.about__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.about__wrapper {
  position: relative;
  z-index: 2;

  max-width: 580px;
  margin: 0 auto;

  @include vp--767 {
    max-width: 320px;
  }
}

.about__title {
  position: relative;

  margin: 0;
  margin-bottom: 45px;
  padding-bottom: 30px;

  font-weight: 700;
  font-size: 64px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;

  &::after {
    position: absolute;
    content: "";
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);

    width: 133px;
    height: 2px;

    background-color: $color-default-white;
  }

  @include vp--767 {
    margin-bottom: 25px;
    padding-bottom: 10px;
    font-size: 52px;

    &::after {
      width: 77px;
    }
  }
}

.about__desc {
  max-width: 520px;
  margin: 0 auto 60px;

  font-size: 18px;
  line-height: 1.11;
  text-align: center;

  @include vp--767 {
    margin-bottom: 25px;

    font-size: 12px;
    line-height: 1.33;
  }
}

.about__achievements-wrapper {
  margin: 0 auto;
}

.about__achievements {
  @include reset-list;

  display: flex;

  @include vp--599 {
    margin: 0 -20px;
  }
}

.about__achievement {
  position: relative;

  min-width: 160px;
  padding-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-align: center;
  text-transform: uppercase;

  &::after {
    position: absolute;
    content: "";
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);

    width: 80px;
    height: 2px;

    background-color: $color-default-white;
  }

  @include vp--767 {
    min-width: 100px;
    padding-bottom: 15px;

    font-size: 12px;
    line-height: 1.33;

    &::after {
      width: 48px;
    }
  }
}

.about__achievement-value {
  display: block;

  margin-bottom: 4px;

  font-weight: 700;
  font-size: 64px;
  line-height: 1.25;

  @include vp--767 {
    font-size: 36px;
    line-height: 1.11;
  }
}
