.slider-special-offers__slide {
  display: flex;
  flex-direction: column;
  height: 300px;
  padding: 40px;
  padding-right: 60px;
  padding-bottom: 30px;

  background-color: $color-default-white;

  @include vp--599 {
    padding: 20px;
    padding-bottom: 30px;
  }
}

.slider-special-offers__slide-title {
  margin: 0;
  margin-bottom: 16px;

  font-weight: 500;
  font-size: 32px;
  line-height: 1.06;
  text-transform: uppercase;

  @include vp--599 {
    padding-right: 20px;

    font-size: 24px;
    line-height: 1.33;
  }
}

.slider-special-offers__slide-desc {
  margin: 0;
  margin-bottom: 20px;

  @include vp--599 {
    max-width: 180px;
    font-size: 12px;
    line-height: 1.33;
  }
}

.slider-special-offers__slide-btn {
  margin-top: auto;
}

.slider-special-offers__btns {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 120px;
  right: 10px;
  z-index: 5;

  height: 80px;

  @include vp--599 {
    top: 110px;
    right: -1px;
  }
}
