.server-response {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 60px 0 100px;

  background-color: $color-whisper-2;

  @include vp--1023 {
    padding-top: 40px;
  }
}

.server-response__img {
  margin-bottom: 25px;

  img {
    width: 215px;
    height: 252px;
    margin: 0 auto;
  }
}

.server-response__title {
  max-width: 320px;
  margin: 0 auto 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  color: $color-kelly-green;

  @include vp--599 {
    font-size: 20px;
    line-height: 1.15;
  }
}

.server-response__desc {
  max-width: 240px;
  margin: 0 auto 20px;

  font-size: 14px;
  line-height: 1.43;
  text-align: center;

  @include vp--599 {

  }
}

.server-response__btn {
  margin: 0 auto;

  transition:
    color $trans--300,
    border-color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
      border-color: $color-tangerine;

      transition:
        color $trans--300,
        border-color $trans--300;
    }
  }
}
