.catalog-filter__fieldset {
  margin: 0;
  padding: 0;

  border: none;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.catalog-filter__legend {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
}

.catalog-filter__checkboxes {
  &--toggle {
    overflow: hidden;
    max-height: 80px;

    transition: max-height $trans--300;
  }

  @include vp--599 {
    &--toggle {
      max-height: 72px;
    }
  }
}

.catalog-filter__checkbox {
  margin-bottom: 0;
}

.catalog-filter__show-all-btn {
  @include reset-btn;
  display: none;
  margin-top: 20px;

  text-decoration-line: underline;
  text-align: left;
  color: $color-kelly-green;

  transition: color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
    }
  }
}
