.account-access {
  padding: 20px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--13;
}

.account-access__content {
  display: flex;

  margin-bottom: 10px;

  font-size: 14px;
  line-height: 1.43;

  > *:not(:last-child) {
    margin-right: 20px;
  }

  @include vp--1279 {
    display: block;

    margin-bottom: 20px;
  }
}

.account-access__user {
  flex: 1 1 240px;
  margin: 0;

  @include vp--1279 {
    margin-bottom: 20px;
  }
}

.account-access__position {
  flex: 1 1 200px;
  margin: 0;
}

.account-access__link-wrapper {
  flex: 1 0 120px;

  text-align: right;

  @include vp--1279 {
    display: block;

    width: fit-content;
    margin-top: 5px;

    text-align: left;
  }
}

.account-access__control {
  font-size: 12px;
  line-height: 1.67;
  text-decoration: underline;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }

  &:not(:last-child) {
    margin-right: 20px;
  }
}
