.partnership-presentation {
  padding: 60px 0;
}

.partnership-presentation__title {
  margin: 0;
  margin-bottom: 15px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  color: $color-kelly-green;

  @include vp--767 {
    margin-bottom: 25px;
  }
}

.partnership-presentation__content {
  margin-bottom: 15px;

  @include vp--767 {
    margin: 0 -15px 25px;
  }
}

.partnership-presentation__btn {
  margin: 0 auto;
}
