.product-characteristics {
  @include vp--1023 {
    max-width: 680px;
  }
}

.product-characteristics__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  color: $color-kelly-green;
}

.product-characteristics__summary {
  margin-bottom: 20px;

  p {
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.product-characteristics__list {
  @include reset-list;
}

.product-characteristics__item {
  padding-bottom: 20px;
}

.product-characteristics__btn {
  @include reset-btn;

  position: relative;

  width: 100%;
  padding-right: 40px;

  font-weight: 500;
  text-align: left;

  transition: color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-kelly-green;
    }
  }
}

.product-characteristics__btn-icon {
  @include reset-btn;
  position: absolute;
  top: 0;
  right: 0;

  width: 20px;
  height: 20px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: $color-kelly-green;

    transition: transform $trans--300;
  }

  &::before {
    width: 18px;
    height: 2px;
  }

  &::after {
    width: 2px;
    height: 18px;
  }

  .product-characteristics__item.is-active & {
    &::before,
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}

.product-characteristics__desc-wrapper {
  padding-top: 20px;

  p {
    margin: 0;
  }
}
