.slider-btn {
  @include reset-btn;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;

  color: $color-default-white;
  background-color: $color-kelly-green;
  border-radius: 50%;

  transition: background-color $trans--300;

  svg {
    width: 14px;
    height: 19px;
  }

  &--prev {
    transform: rotate(-180deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      background-color: $color-fun-green;
    }
  }

  &--bright {
    background-color: $color-tangerine;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-tawny;
      }
    }
  }

  &--figured {
    height: 39px;
    border-radius: 10px 0 0 0;
  }
}
