.slider-categories {
  position: relative;

  width: 1180px;
  margin: 0 auto;

  @include vp--1279 {
    width: 628px;
  }

  @include vp--767 {
    width: 280px;
    padding-bottom: 45px;
  }
}

.slider-categories__container {
  position: relative;
  width: 1090px;
  margin: 0 auto;
  padding: 15px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    width: 10px;
    z-index: 2;
  }

  &::before {
    left: 0;

    background-image: $slider-left-050;
  }

  &::after {
    right: 0;

    background-image: $slider-rigth;
  }

  @include vp--1279 {
    width: 550px;
  }

  @include vp--767 {
    width: 280px;
  }
}

.slider-categories__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;

  &.slider-btn--prev {
    left: 0;
    transform: translateY(-50%) rotate(-180deg);
  }

  &.slider-btn--next {
    right: 0;
  }

  @include vp--767 {
    top: unset;
    bottom: 0;
    transform: unset;

    &.slider-btn--prev {
      left: 90px;
      transform: rotate(-180deg);
    }

    &.slider-btn--next {
      right: 90px;
    }
  }
}
