.toggle-group {
  position: relative;

  display: flex;
  flex-direction: column;

  &__error {
    position: absolute;
    left: 30px;
    bottom: 5px;

    font-size: 12px;
    line-height: 1.33;
    color: $color-fire-engine-red;

    opacity: 0;

    transition: opacity $trans--300;
  }

  &.is-invalid {
    label span {
      color: $color-fire-engine-red;
    }

    .toggle-group__error {
      opacity: 1;
    }
  }
}
