.cart-card {
  display: grid;
  grid-template-columns: 180px 180px 1fr minmax(160px, 1fr) 80px 80px minmax(100px, 1fr);
  grid-template-rows: 1fr auto;
  gap: 20px;

  @include vp--1023 {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
  }

  @include vp--599 {
    grid-template-columns: repeat(2, 1fr);
  }
}

.cart-card__img {
  position: relative;
  grid-row: 1 / 3;

  width: 100%;
  padding-top: percentage(22 / 18);

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
  }

  @include vp--599 {
    grid-row: 1 / 2;
  }
}

.cart-card__title {
  padding-top: 20px;

  @include vp--1023 {
    grid-column: 2 / -1;

    padding-top: 0;
  }
}

.cart-card__title-link {
  margin: 0;

  @include vp--599 {
    font-size: 12px;
    line-height: 1.5;
  }
}

.cart-card__price{
  padding-top: 20px;

  @include vp--1023 {
    padding-top: 0;
  }
}

.cart-card__price-inner {
  &:not(:last-child) {
    margin-bottom: 14px;
  }
}

.cart-card__price-value {
  margin: 0;
  margin-bottom: 5px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;
}

.cart-card__price-desc {
  margin: 0;

  font-size: 12px;
  line-height: 1.67;
  color: $color-black-060;
}

.cart-card__quanttity {
  padding-top: 20px;

  @include vp--1023 {
    padding-top: 0;
  }
}

.cart-card__counter {
  margin-bottom: 20px;
}

.cart-card__quanttity-inner {
  margin: 0;

  font-size: 12px;
  line-height: 1.67;

  @include vp--599 {
    span {
      display: none;
    }
  }
}

.cart-card__legend {
  display: none;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;

  @include vp--1023 {
    display: inline;

    line-height: 1.25;
  }
}

.cart-card__weight {
  margin: 0;
  padding-top: 20px;

  font-size: 16px;
  line-height: 1.25;

  @include vp--1023 {
    border-top: 1px solid $color-whisper;
  }

  @include vp--599 {
    grid-column: 1 / -1;
  }
}

.cart-card__bonus {
  margin: 0 -20px;
  padding: 20px 20px 0;

  font-weight: 500;

  @include vp--1023 {
    display: flex;

    border-top: 1px solid $color-whisper;
  }

  @include vp--599 {
    grid-column: 1 / -1;

    margin: 0;
    margin-top: -10px;
    padding: 0;

    border-top: none;
  }
}

.cart-card__bonus-icon {
  position: relative;
  transform: translateY(-4px);

  color: $color-tangerine;

  svg {
    display: block;

    width: 30px;
    height: 30px;
  }

  @include vp--1023 {
    margin-left: 10px;

    svg {
      width: 20px;
      height: 20px;
    }
  }
}

.cart-card__total-price {
  margin: 0;
  padding-top: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;

  @include vp--1023 {
    border-top: 1px solid $color-whisper;
  }

  @include vp--599 {
    grid-column: 1 / -1;
  }

  @include vp--599 {
    grid-column: 1 / -1;

    margin-top: -10px;
    padding: 0;

    border-top: none;
  }
}

.cart-card__remove-option {
  position: relative;
  grid-column: 2 / -1;

  @include vp--1023 {
    grid-column: 1 / -1;

    padding: 20px 0;

    border-top: 1px solid $color-whisper;
    border-bottom: 1px solid $color-whisper;
  }

  @include vp--599 {
    padding: 10px 0;
  }
}

.cart-card__remove-link {

  width: max-content;
  padding-left: 25px;
  font-size: 12px;
  line-height: 1.67;
  color: $color-black-060;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 0;

    width: 18px;
    height: 2px;

    background-color: $color-kelly-green;

    transition: background-color $trans--300;
  }

  &::before {
    transform: translatey(-50%) rotate(45deg);
  }

  &::after {
    transform: translatey(-50%) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;

      &::before,
      &::after {
        background-color: $color-tangerine;
      }
    }
  }
}
