.autentification {
  padding: 80px 0 100px;

  @include vp--1023 {
    padding-top: 60px;
  }

  @include vp--767 {
    padding-top: 40px;
  }
}

.autentification__title {
  margin: 0;
  padding-bottom: 60px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;

  color: $color-kelly-green;

  border-bottom: 1px solid $color-kelly-green;

  @include vp--767 {
    padding-bottom: 40px;
  }
}

.autentification__desc {
  max-width: 580px;
  margin: 40px 0;

  @include vp--599 {
    &:not(.autentification__desc--increased-margin) {
      margin-bottom: 20px;
    }
  }
}

.autentification__form-title {
  max-width: 580px;
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
}

.autentification__recovery-input {
  width: 240px;
}
