.slider-product-gallery {
  display: flex;

  width: 100%;
  height:100%;

  @include vp--599 {
    flex-direction: column-reverse;
  }
}

.slider-product-gallery__thumbs {
  flex-shrink: 0;

  width: 80px;
  height: 100%;
  margin-right: 20px;

  @include vp--599 {
    width: 100%;
    margin-top: 12px;
    margin-right: 0;
  }
}

.slider-product-gallery__thumbs-wrapper {
  height: 380px;

  @include vp--599 {
    height: 100%;
  }
}

.slider-product-gallery__thumbs-slide {
  cursor: pointer;
}

.slider-product-gallery__thumbs-slide-wrapper {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp--599 {
    position: relative;
    padding-top: 100%;

    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}

.slider-product-gallery__btn {
  @include reset-btn;

  position: absolute;
  content: "";
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;

  width: 40px;
  height: 40px;

  &--prev {
    top: -10px;
    transform: translateX(-50%) rotate(-180deg);
  }

  &--next {
    bottom: -10px;
  }

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;

    width: 8px;
    height: 2px;
    background-color: $color-black;

    transition: background-color $trans--300;
  }

  &::before {
    right: 50%;
    transform-origin: right bottom;
    transform: translateY(1px) rotate(45deg);
  }

  &::after {
    left: 50%;
    transform-origin: left bottom;
    transform: translateY(1px) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-kelly-green;
      }
    }
  }

  @include vp--599 {
    top: 50%;
    transform: translateY(-50%);

    &--prev {
      left: -10px;
      transform: translateY(-50%) rotate(90deg);
    }

    &--next {
      left: unset;
      right: -10px;
      transform: translateY(-50%) rotate(-90deg);
    }
  }
}

.slider-product-gallery__photos {
  width: calc(100% - 100px);
  height: 100%;

  @include vp--599 {
    width: 100%;
    height: 340px;
  }
}

.slider-product-gallery__photos-slide {
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
