.media-card {
  overflow: hidden;
  display: flex;

  max-width: 720px;
  min-height: 240px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $shadow--3;

  @include vp--599 {
    position: relative;
    display: block;

    padding-top: 85%;
  }

  &--size-l {
    max-width: unset;
    min-height: 360px;

    border-radius: 5px;

    @include vp--1023 {
      position: relative;
      display: block;

      padding-top: 62%;
    }

    @include vp--599 {
      padding-top: 62%;
    }
  }
}

.media-card__cover {
  flex-shrink: 0;

  width: 280px;
  min-height: 100%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp--599 {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    width: 100%;
    min-height: unset;
    height: calc((100vw - 2 * 20px) * 0.85);
  }

  .media-card--size-l & {
    width: 50%;
    max-width: 580px;

    @include vp--1023 {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;

      width: 100%;
      max-width: unset;
      min-height: unset;
      height: calc((100vw - 2 * 30px) * 0.62);
    }

    @include vp--599 {
      height: calc((100vw - 2 * 20px) * 0.62);
    }
  }
}

.media-card__content {
  display: flex;
  flex-direction: column;

  padding: 20px;

  .media-card--size-l & {
    padding: 40px;

    @include vp--599 {
      padding: 20px;
    }
  }
}

.media-card__title {
  margin: 0;
  margin-bottom: 20px;

  .media-card--size-l & {
    color: $color-kelly-green;
  }
}

.media-card__lead {
  flex-shrink: 1;
  margin: 0;
  margin-bottom: 20px;

  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;

  .media-card--size-l & {
    -webkit-line-clamp: 9;

    @include vp--599 {
      -webkit-line-clamp: 6;
    }
  }
}

.media-card__link {
  display: block;

  width: fit-content;
  margin-top: auto;

  font-size: 12px;
  line-height: 1.67;
  text-decoration-line: underline;
  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}
