.footer-contacts {
  position: relative;
  padding: 40px 0 100px;
  z-index: 0;

  text-align: right;

  @include vp--1023 {
    position: relative;
    text-align: left;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: -100px;
      right: -100px;
      bottom: 0;
      z-index: -1;

      background-color: $color-kelly-green;
    }
  }
}

.footer-contacts__wrapper {
  margin-bottom: 60px;

  color: $color-default-white;
}

.footer-contacts__title {
  margin: 0;
  margin-bottom: 20px;
}

.footer-contacts__address {
  margin: 0;
  margin-bottom: 20px;
}

.footer-contacts__link {
  display: block;

  width: max-content;
  max-width: 100%;
  margin-left: auto;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }

  &--accent {
    text-decoration: underline;
  }

  @include vp--1023 {
    margin-left: 0;
  }
}
