.slider-partnership {
  margin: 0 -15px;

  @include vp--1279 {
    width: 610px;
    margin: 0 auto;
  }

  @include vp--767 {
    width: 310px;
    padding-bottom: 15px;
  }
}

.slider-partnership__container {
  overflow: auto;
  position: static;
  z-index: unset;

  margin-left: unset;
  margin-right: unset;
  padding: 0;

  @include vp--767 {
    overflow: hidden;
    position: relative;
    z-index: 1;

    margin-left: auto;
    margin-right: auto;
    padding: 15px;

    &::before,
    &::after {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      width: 10px;
      z-index: 2;
    }

    &::before {
      left: 0;

      background-image: $slider-left-050;
    }

    &::after {
      right: 0;

      background-image: $slider-rigth;
    }
  }
}

.slider-partnership__wrapper {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: static;
  z-index: unset;

  width: unset;
  height: unset;
  padding: 15px;

  transform: unset;

  transition-property: unset;

  @include vp--767 {
    box-sizing: content-box;
    display: flex;
    flex-wrap: unset;
    justify-content: unset;
    position: relative;
    z-index: 1;

    width: 100%;
    height: 100%;
    padding: 0;

    transform: translate3d(0, 0, 0);

    transition-property: transform;
  }
}

.slider-partnership__slide {
  flex-shrink: unset;
  position: static;

  width: unset;
  height: unset;
  margin: 10px 0;

  transition-property: unset;

  @include vp--767 {
    flex-shrink: 0;
    position: relative;

    width: 100%;
    height: 100%;
    margin: unset;

    transition-property: transform;
  }
}

.slider-partnership__pagination {
  display: none;
  justify-content: center;

  margin-top: 15px;

  @include vp--767 {
    display: flex;
  }
}

.slider-partnership__bullet {
  display: block;

  width: 10px;
  height: 10px;
  margin: 0 5px;

  background-color: $color-kelly-green;
  border-radius: 50%;
}

.slider-partnership__active-bullet {
  background-color: $color-fun-green;
}
