.partnership-card {
  overflow: hidden;
  display: block;

  width: 280px;

  color: $color-default-white;
  border-radius: 5px;
  box-shadow: $complex-shadow--1;
}

.partnership-card__icon {
  display: flex;
  justify-content: center;
  align-items: flex-end;

  width: 100%;
  height: 130px;
  padding: 20px;

  background-color: $color-default-white;
}

.partnership-card__content {
  display: flex;
  justify-content: center;
  align-items: center;

  min-height: 90px;
  padding: 20px;

  background-color: $color-kelly-green;

  .category-card--size-s & {
    min-height: 80px;
    padding: 10px 15px;
  }
}

.partnership-card__btn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  margin: 0;
  min-height: 50px;
  padding: 20px;
  padding-top: 3px;
  padding-bottom: 5px;

  font-weight: bold;
  font-size: 16px;
  line-height: 1.12;
  text-align: center;
  text-transform: uppercase;

  background-color: $color-kelly-green;
  border-radius: 25px;
  box-shadow: $complex-shadow--4;

  animation: partnership-card-btn $trans--200;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      padding-top: 5px;
      padding-bottom: 3px;

      box-shadow: $complex-shadow--5;
      animation: partnership-card-btn-hover $trans--200;
    }
  }
}

@keyframes partnership-card-btn {
  0% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: $complex-shadow--5;
  }

  50% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: none;
  }

  100% {
    padding-top: 3px;
    padding-bottom: 5px;

    box-shadow: $complex-shadow--4;
  }
}

@keyframes partnership-card-btn-hover {
  0% {
    padding-top: 3px;
    padding-bottom: 5px;

    box-shadow: $complex-shadow--4;
  }

  50% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: none;
  }

  100% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: $complex-shadow--5;
  }
}
