.product {
  padding: 30px 0 60px;

  @include vp--1023 {
    padding: 10px 0 40px;
  }
}

.product__title {
  margin: 10px 0 30px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  color: $color-tangerine;
}

.product__header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  margin-bottom: 10px;
}

.product__id {
  width: 160px;
  margin: 0;
  margin-right: 40px;

  font-size: 12px;
  line-height: 1.67;
}

.product__header-links {
  display: flex;
  flex-wrap: wrap;
}

.product__header-link {
  padding: 10px 0;

  color: $color-kelly-green;
  text-decoration: underline;

  &:not(:last-child) {
    margin-right: 40px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.product__details {
  display: flex;
  align-items: flex-start;

  max-width: 1080px;
  margin-bottom: 60px;

  @include vp--1023 {
    display: block;

    margin-bottom: 40px;
  }
}

.product__photo-gallery {
  width: calc(100% - 400px);
  height: 380px;
  margin-right: 20px;

  @include vp--1023 {
    width: 100%;
    max-width: 680px;
    margin-bottom: 20px;
  }

  @include vp--599 {
    height: unset;
  }
}

.product__info {
  display: flex;
  flex-direction: column;

  min-height: 380px;
  padding: 19px;

  border: 1px solid $color-whisper-2;

  @include vp--1023 {
    width: 100%;
    max-width: 680px;
    min-height: unset;
    margin-bottom: 20px;
  }
}

.product__price {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 20px 10px;

  margin-bottom: 20px;
}

.product__price-value {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;
}

.product__price-desc {
  margin: 0;

  font-size: 12px;
  line-height: 1.67;

  color: $color-black-060;
}

.product__desc {
  margin-bottom: 20px;

  p {
    margin: 0;
  }
}

.product__quanttity {
  margin: auto 0 20px;

  @include vp--1023 {
    margin-top: 0;
  }
}

.product__quanttity-inner {
  margin: 0;

  font-size: 12px;
  line-height: 1.67;
}

.product__actions {
  display: flex;
}

.product__add-to-cart-btn {
  min-width: unset;
  width: 180px;
}

.product__share-link {
  display: flex;
  align-items: center;

  padding: 10px 0;

  font-size: 12px;
  line-height: 1.67;

  svg {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }

  &:not(:first-child) {
    margin-left: 20px;
  }
}

.product__share-desc {
  @include vp--599 {
    display: none;
  }
}

.product__counter {
  flex-basis: 180px;

  input {
    padding: 10px 75px;
  }

  .cart-product-counter__quantity-btn{
    &--decrement {
      left: 52px;
    }

    &--increment {
      right: 52px;
    }
  }
}
