.distribution-channels {
  padding: 60px 0;

  @include vp--1023 {
    padding: 40px 0;
  }
}

.distribution-channels__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.distribution-channels__desc {
  margin: 0;
  margin-bottom: 60px;
}

.distribution-channels__directions-title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: $color-tangerine;
}

.distribution-channels__directions {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;

  @include vp--1023 {
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp--599 {
    grid-template-columns: repeat(1, 1fr);
  }
}

.distribution-channels__direction {
  display: flex;
  align-items: center;

  min-height: 100px;
  padding: 20px;

  background: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--8;

  svg {
    flex-shrink: 0;

    width: 40px;
    height: 40px;

    color: $color-tangerine;
  }

  @include vp--767 {
    min-height: 80px;
    padding: 15px 20px;
  }
}

.distribution-channels__direction-name {
  margin: 0;
  margin-left: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  @include vp--767 {
    font-size: 14px;
    line-height: 1.29;
  }
}
