.slider-photos {
  position: relative;
}

.slider-photos__slide {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.slider-photos__btns {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
  transform: translateY(-50%);

  height: 80px;

  @include vp--1023 {
    right: -1px;
  }
}
