.custom-select {
  position: relative;
  z-index: 10;

  width: 100%;
  margin-bottom: 20px;

  select {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  &.open-up {
    .custom-select__list {
      bottom: auto;
      top: 0;
      transform: translateY(calc(-100% + 30px));
    }
  }

  &.is-open {
    .custom-select__list {
      visibility: visible;
      opacity: 1;
    }

    .custom-select__button {
      &:hover,
      &:focus {
        color: $color-black-060;
      }
    }

    .custom-select__icon {
      transform: rotate(180deg);
    }

    // .custom-select__label {
      // opacity: 0;
    // }
  }

  &.not-empty {
    .custom-select__text {
      opacity: 1;
    }

    .custom-select__label {
      opacity: 0;
    }

    .custom-select__placeholder {
      opacity: 0;
    }
  }

  &.is-invalid {
    .custom-select__error {
      opacity: 1;
    }

    .custom-select__button {
      border-color: $color-fire-engine-red;

      &:focus,
      &:hover {
        border-color: $color-fire-engine-red;
      }
    }
  }

  // &.is-valid {
    // .custom-select__button {
    //   border: 1px solid green;

    //   &:focus,
    //   &:hover {
    //     border: 1px solid rgba(green, 0.5);
    //   }
    // }
  // }

  &.slide-up.not-empty,
  &.slide-up.is-open {
    .custom-select__label {
      transform: translateY(calc(-50% - 40px));
      opacity: 1;
    }

    .custom-select__text {
      opacity: 1;
    }
  }

  &--size-s {
    width: 180px;
    max-width: unset;

    @include vp--599 {
      width: 124px;
    }
  }

  &--header{
    @include vp--599 {
      width: 180px;
    }
  }
}

.custom-select__label {
  position: absolute;
  top: 50%;
  left: 10px;

  transform: translateY(-50%);

  color: $color-black-060;

  transition:
    border-color $trans--300,
    transform $trans--300;

  pointer-events: none;

  .custom-select__button:hover ~ &,
  .custom-select__button:focus ~ & {
    color: $color-kelly-green;
  }

  .custom-select--size-s & {
    left: 20px;
  }

  @include vp--599 {
    .custom-select--size-s & {
      left: 10px;

      font-size: 12px;
      line-height: 1.67;
    }

    .custom-select--header & {
      font-size: 14px;
      line-height: 1.43;
    }
  }
}

.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 10px;

  transform: translateY(-50%);

  transition: transform $trans--300, opacity $trans--300;

  pointer-events: none;
}

.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 40px;
  padding: 9px;
  padding-left: 19px;
  padding-right: 14px;

  font-size: 14px;
  line-height: 1.43;
  color: $color-black-060;

  background-color: $color-transparent;
  border: 1px solid $color-whisper;
  border-radius: 5px;
  outline: none;
  cursor: pointer;

  transition: border-color $trans--300;

  &:hover,
  &:focus {
    color: $color-kelly-green;
  }

  .custom-select--size-s & {
    min-height: 30px;
    padding: 4px;
    padding-right: 14px;
    padding-left: 19px;

    transition: color $trans--300;
  }

  .custom-select--header & {
    padding-right: 4px;
    padding-left: 9px;

    border-color: $color-transparent;
  }

  @include vp--599 {
    .custom-select--size-s & {
      padding-left: 9px;
    }

    .custom-select--header & {
      padding-left: 19px;
    }
  }
}

.custom-select__list {
  position: absolute;
  top: 100%;
  z-index: 1;

  width: 100%;
  max-height: 240px;
  margin: 0;
  padding: 0;
  overflow-y: auto;

  list-style: none;

  background-color: $color-default-white;
  border-radius: 5px;
  box-shadow: $shadow--4;
  visibility: hidden;
  opacity: 0;

  transition: visibility $trans--300, opacity $trans--300;

  .custom-select--size-s & {
    max-height: 180px;

    background-color: $color-default-white;
    border-radius: 0 0 5px 5px;
    box-shadow: $shadow--5;
  }
}

.custom-select__item {
  padding: 10px;
  padding-left: 20px;
  padding-right: 60px;

  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  outline: none;
  cursor: pointer;

  transition: background-color $trans--300;

  &[aria-selected="true"] {
    background-color: $color-whisper-2;
    background-image: url("../img/svg/icon-check.svg");
    background-repeat: no-repeat;
    background-position: right 20px center;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-whisper-2;
      }
    }
  }

  &--complex {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    padding: 0;
    padding-top: 10px;

    &[aria-selected="true"] {
      background-position: right 10px bottom 10px;
    }
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      background-color: $color-whisper-2;
    }
  }

  .custom-select--size-s & {
    padding: 5px;
    padding-left: 20px;
    padding-right: 30px;

    font-size: 12px;
    line-height: 1.67;

    &[aria-selected="true"] {
      background-color: $color-white-smoke;
      background-position: right 4px center;

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          background-color: $color-white-smoke;
        }
      }
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-white-smoke;
      }
    }
  }

  .custom-select--header & {
    padding-left: 10px;

    text-align: right;

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  @include vp--599 {
    .custom-select--size-s & {
      padding-left: 10px;

      overflow: visible;
      white-space: unset;
      text-overflow: unset;

      font-size: 10px;
      line-height: 1.4;
    }

    .custom-select--header & {
      padding-left: 20px;

      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;

      font-size: 12px;
      line-height: 1.67;
    }
  }
}

.custom-select__error {
  position: absolute;
  right: 0;
  top: 100%;

  font-size: 12px;
  line-height: 1.33;
  color: $color-fire-engine-red;

  opacity: 0;

  transition: opacity $trans--300;
}

.custom-select__text {
  flex-shrink: 0;
  width: calc(100% - 25px);
  overflow: hidden;

  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;

  opacity: 0;

  br {
    display: none;
  }

  .custom-select--size-s & {
    flex-shrink: 0;
    overflow: hidden;
  }

  .custom-select--header & {
    text-align: right;
  }

  @include vp--599 {
    .custom-select--size-s & {
      font-size: 12px;
      line-height: 1.67;
    }

    .custom-select--header & {
      font-size: 14px;
      line-height: 1.43;
    }
  }
}

.custom-select__icon {
  flex-shrink: 0;
  width: 10px;
  height: 5px;
  margin-left: auto;

  background-color: $color-kelly-green;

  transition: transform $trans--300;

  clip-path: polygon(0 0, 100% 0, 50% 100%);
}

.custom-select--placeholder {
  .custom-select__label {
    transform: translateY(calc(-50% - 40px));
    opacity: 1;
  }

  &.is-open .custom-select__label {
    opacity: 1;
  }

  &.not-empty .custom-select__label {
    opacity: 1;
  }
}
