.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.5);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans--600,
    transform $trans--600,
    visibility $trans--600;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 30px;

    @include vp--599 {
      padding: 60px 20px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    overflow: hidden;
    position: relative;

    width: 100%;
    max-width: 520px;
    padding: 40px;

    background-color: $color-default-white;
    border-radius: 5px;
    box-shadow: $shadow--7;

    @include vp--599 {
      padding: 40px 20px;
    }

    .modal--size-s & {
      max-width: 280px;
      padding: 20px;

      @include vp--599 {
        padding: 20px;
      }
    }

    .modal--add-to-cart & {
      max-width: 540px;
      padding: 20px;
    }

    button {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__close-btn {
    @include reset-btn;
    position: absolute;
    top: 10px;
    right: 10px;

    width: 40px;
    height: 40px;
    padding: 0;

    border: none;
    cursor: pointer;

    &::before,
    &::after {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;

      width: 18px;
      height: 2px;

      background-color: $color-black;

      transition: background-color $trans--300;
    }

    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        &::before,
        &::after {
          background-color: $color-kelly-green;
        }
      }
    }

    .modal--auto-close & {
      display: none;
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp--599 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}
