.category-card {
  overflow: hidden;
  display: block;

  width: 280px;

  color: $color-default-white;
  border-radius: 5px;
  box-shadow: $complex-shadow--1;

  &--size-s {
    width: 250px;
  }

  &--flat {
    position: relative;
    width: 100%;
    padding-top: 100%;
  }
}

.category-card__img {
  overflow: hidden;

  width: 100%;
  height: 190px;

  background-color: $color-default-white;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .category-card--size-s & {
    height: 170px;
  }

  .category-card--flat & {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 32.14%;

    height: 67.86%;
  }
}

.category-card__content {
  display: flex;
  justify-content: center;
  align-items: center;

  min-height: 90px;
  padding: 20px 30px;

  background-color: $color-tangerine;

  .category-card--size-s & {
    min-height: 80px;
    padding: 10px 15px;
  }

  .category-card--flat & {
    position: absolute;
    top: 67.86%;
    left: 0;
    right: 0;
    bottom: 0;

    min-height: unset;
    height: 32.14%;

    transition: background-color $trans--300;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    .category-card--flat:hover &,
    .category-card--flat:focus & {
      background-color: $color-kelly-green;
    }
  }
}

.category-card__btn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 214px;
  margin: 0;
  min-height: 44px;
  padding: 20px;
  padding-top: 3px;
  padding-bottom: 5px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;
  text-align: center;
  text-transform: uppercase;

  background-color: $color-tangerine;
  border-radius: 22px;
  box-shadow: $complex-shadow--2;

  animation: category-card-btn $trans--200;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      padding-top: 5px;
      padding-bottom: 3px;

      box-shadow: $complex-shadow--3;
      animation: category-card-btn-hover $trans--200;
    }
  }
}

.category-card__title {
  margin: 0;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;

  @include vp--767 {
    font-size: 14px;
    line-height: 1.29;
  }

  @include vp--767 {
    font-size: 10px;
    line-height: 1.2;
  }
}

@keyframes category-card-btn {
  0% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: $complex-shadow--3;
  }

  50% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: none;
  }

  100% {
    padding-top: 3px;
    padding-bottom: 5px;

    box-shadow: $complex-shadow--2;
  }
}

@keyframes category-card-btn-hover {
  0% {
    padding-top: 3px;
    padding-bottom: 5px;

    box-shadow: $complex-shadow--2;
  }

  50% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: none;
  }

  100% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: $complex-shadow--3;
  }
}
