.ordering {
  padding: 60px 0 100px;

  @include vp--1023 {
    padding-top: 40px;
  }
}

.ordering__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: #43A10B;
}
.ordering__form-wrapper {
  display: flex;
  align-items: flex-start;

  @include vp--1023 {
    flex-direction: column;
  }
}

.ordering__form-divisions {
  width: 100%;
  max-width: 680px;
  margin-right: 20px;

  @include vp--1023 {
    margin-right: 0;
    margin-bottom: 40px;
  }
}

.ordering__form-division {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.ordering__form-subtitle {
  margin: 0;
  margin-bottom: 20px;
}

.ordering__form-city {
  position: relative;
}

.ordering__search-icon {
  position: absolute;
  top: calc(30px / 2 + 50%);
  left: 20px;
  transform: translateY(-50%);

  color: $color-kelly-green;

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }
}

.ordering__city-input {
  label {
    flex-direction: column;
    align-items: flex-start;

    input {
      width: 100%;
      padding-left: 49px;
      padding-right: 49px;
    }
  }

  .custom-input__label {
    margin-bottom: 10px;
  }
}

.ordering__form-reset-bth {
  @include reset-btn;
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translateY(calc(30px / 2 - 50%));

  width: 40px;
  height: 40px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 18px;
    height: 2px;

    background-color: $color-black;

    transition: background-color $trans--300;
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-kelly-green;
      }
    }
  }
}

.ordering__radio-group {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.ordering__delivery-type {
  width: 260px;
  margin-right: 40px;
}

.ordering__inputs-wrapper {
  width: 100%;
  max-width: 380px;
}

.ordering__input {
  .custom-input__label {
    flex-shrink: 0;
  }

  input {
    width: 100%;
  }

  @include vp--599 {
    label {
      flex-direction: row;
      align-items: center;
    }

    .custom-input__label {
      width: 148px;
      margin-right: 10px;
    }
  }
}

.ordering__address-input {
  label {
    flex-direction: column;
    align-items: flex-start;

    input {
      width: 100%;
    }
  }

  .custom-input__label {
    margin-bottom: 10px;
  }
}

.ordering__inputs-row {
  display: flex;
  justify-content: space-between;

  .ordering__input {
    width: calc(50% - 10px);
  }
}

.ordering__carriers {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  margin: 0 -10px 10px;
}

.ordering__carrier {
  width: 50%;
  max-width: 180px;

  padding: 10px;
  padding-top: 0;

  label {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 60px;
    margin-bottom: 10px;

    border: 1px solid $color-whisper-2;

    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
  }

  input:checked + label {
    border-color: $color-kelly-green;
  }

  label:hover,
  input:focus + label {
    border-color: $color-kelly-green;
  }
}

.ordering__carrier-name {
  margin: 0;

  font-size: 14px;
  line-height: 1.29;

  @include vp--599 {
    font-size: 12px;
    line-height: 1.5;
  }
}

.ordering__carrier-cost {
  margin: 0;

  font-size: 14px;
  line-height: 1.29;

  @include vp--599 {
    font-size: 12px;
    line-height: 1.5;
  }
}

.ordering__date {
  position: relative;
  margin-bottom: 40px;

  &::after {
    position: absolute;
    content: "";
    top: 50%;
    right: 17px;
    transform: translateY(-50%);

    width: 10px;
    height: 5px;
    margin-left: auto;

    background-color: $color-kelly-green;

    transition: transform $trans--300;

    clip-path: polygon(0 0, 100% 0, 50% 100%);
  }
}

.ordering__delivery-cost {
  margin: 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
}

.ordering__form-summary {
  flex-shrink: 0;
  width: 280px;
  padding: 20px;

  border-radius: 10px;
  box-shadow: $shadow--3;
}

.ordering__summary-row {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 20px;

  font-size: 14px;
  line-height: 1.43;

  &--total {
    margin-top: 40px;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.25;
  }
}

.ordering__summary-value {
  font-weight: 500;

  .ordering__summary-row--total & {
    font-weight: 700;
  }
}
