.main-nav__wrapper {
  padding: 15px 0;

  @include vp--1023 {
    position: absolute;
    top: 90px;
    right: 0;
    left: 0;

    height: 100vh;
    /* stylelint-disable-next-line */
    height: calc(100 * var(--vh, 1vh));

    overflow: hidden;
    visibility: hidden;
    opacity: 0;

    padding: 0;

    background-color: $color-default-black-030;

    .main-nav.is-close & {
      transition:
        overflow $trans--300,
        visibility $trans--300,
        opacity $trans--300;
    }

    .main-nav.is-open & {
      overflow: visible;
      visibility: visible;
      opacity: 1;

      transition:
        overflow $trans--300,
        visibility $trans--300,
        opacity $trans--300;
    }
  }

  @include vp--599 {
    top: 70px;
  }
}

.main-nav__overlay {
  @include vp--1023 {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
  }
}

.main-nav__close-btn {
  @include reset-btn;
  display: none;
  position: absolute;
  top: 30px;
  left: 60px;
  z-index: 3;

  width: 40px;
  height: 40px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 18px;
    height: 2px;

    background-color: $color-black;

    transition: background-color $trans--300;
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $color-kelly-green;
      }
    }
  }

  @include vp--1023 {
    display: block;
  }

  @include vp--599 {
    left: 10px;
  }
}

.main-nav__list {
  @include reset-list;
  position: relative;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -10px;

  @include vp--1023 {
    overflow-y: auto;
    display: block;

    max-height: calc(100vh - 100px);
    /* stylelint-disable-next-line */
    max-height: calc(100 * var(--vh, 1vh) - 100px);

    margin: 0;
    padding: 30px;
    padding-left: 100px;
    padding-right: 70px;

    background-color: $color-default-white;
    border-radius: 0 0 5px 5px;
    box-shadow: $shadow--6;
  }

  @include vp--599 {
    padding-left: 50px;
    padding-right: 40px;
  }
}

.main-nav__item {
  flex: 1 0 0;

  margin: 0 10px;

  &--mobile-only {
    display: none;
  }

  @include vp--1023 {
    position: relative;
    margin: 0;

    &--mobile-only {
      display: block;
    }
  }
}

.main-nav__location {
  width: 180px;

  @include vp--1023 {
    margin-left: auto;
    padding: 5px 0;
  }
}

.main-nav__location-select {
  z-index: 11;

  margin-bottom: 0;

  .custom-select__button {
    padding: 5px;
    padding-left: 10px;

    color: $color-black-060;

    border: none;

    transition: color $trans--300;

    &:hover,
    &:focus {
      color: $color-kelly-green;
    }
  }

  .custom-select__list {
    max-height: 180px;

    background-color: $color-default-white;
    border-radius: 0 0 5px 5px;
    box-shadow: $shadow--5;
  }

  .custom-select__item {
    height: 30px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 30px;

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    font-size: 12px;
    line-height: 1.67;
    text-align: right;

    &[aria-selected="true"] {
      background-color: $color-white-smoke;
      background-position: right 4px center;

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          background-color: $color-white-smoke;
        }
      }
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-white-smoke;
      }
    }
  }

  .custom-select__text {
    flex-shrink: 0;
    overflow: hidden;

    text-align: right;
  }
}

.main-nav__phone-link {
  width: fit-content;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  color: $color-default-black;

  @include vp--1023 {
    display: block;

    margin-left: auto;
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 10px;
  }
}

.main-nav__link {
  display: block;

  width: fit-content;
  margin: 0 auto;
  padding: 5px 10px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  @include vp--1023 {
    margin: 0;
    margin-left: auto;
    margin-right: 20px;
    padding: 10px;
  }
}

.main-nav__sublist {
  @include reset-list;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: flex-start;

  position: absolute;
  top: calc(100% + 15px);
  left: 0;
  right: 0;
  padding: 15px;

  background-color: $color-default-white;
  border-radius: 0 0 5px 5px;
  box-shadow: $shadow--6;

  transition:
    max-height $trans--300,
    overflow $trans--300,
    visibility $trans--300,
    opacity $trans--300;

  &::before {
    position: absolute;
    content: "";
    top: -15px;
    left: 0;
    right: 0;
    bottom: calc(100% - 5px);
  }

  &[data-accordion="content"] {
    max-height: 240px;
  }

  &:hover,
  &:focus-within,
  .main-nav__link:hover + &,
  .main-nav__link:focus + & {
    overflow: visible;
    visibility: visible;
    opacity: 1;
  }

  @include vp--1023 {
    visibility: hidden;
    position: relative;
    overflow: hidden;

    display: block;
    padding: 0;

    background-color: $color-transparent;
    border-radius: 0;
    box-shadow: none;

    &[data-accordion="content"] {
      max-height: 0;
    }

    .is-active & {
      overflow: visible;
      visibility: visible;
      opacity: 1;
    }

    &:hover,
    &:focus-within,
    .main-nav__link:hover + &,
    .main-nav__link:focus + & {
      overflow: hidden;
    }
  }
}

.main-nav__subitem {
  width: 260px;
  margin-right: 20px;

  @include vp--1023 {
    width: 100%;
    margin-right: 0;
  }
}

.main-nav__sublink {
  display: block;

  width: fit-content;
  padding: 5px;

  color: $color-black;

  .main-nav__sublist &[href]:hover,
  .main-nav__sublist &:focus {
    color: $color-black;
    text-decoration: underline;
  }

  @include vp--1023 {
    margin-left: auto;
    margin-right: 20px;
    padding: 0 10px;

    line-height: 30px;
    color: $color-black-070;

    .main-nav__sublist &[href]:hover,
    .main-nav__sublist &:focus {
      color: $color-black-070;
    }
  }
}

.main-nav__tab-btn {
  @include reset-btn;
  display: none;
  position: absolute;
  top: 0;
  right: -10px;

  width: 40px;
  height: 40px;

  @include vp--1023 {
    display: block;
  }
}

.main-nav__tab-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 10px;
  height: 5px;

  background-color: $color-kelly-green;

  transition: transform $trans--300;

  clip-path: polygon(0 0, 100% 0, 50% 100%);

  .main-nav__item.is-active & {
    transform: translate(-50%, -50%) rotate(180deg);
  }

  .main-nav__tab-btn:hover &,
  .main-nav__tab-btn:focus & {
    background-color: $color-tangerine;
  }
}
