// Colors
//---------------------------------
// https://www.htmlcsscolor.com/hex/334482

// Default

$color-default-black:             #000000;
$color-default-black-030:         rgba(#000000, 0.3);
$color-default-black-025:         rgba(#000000, 0.25);
$color-default-black-015:         rgba(#000000, 0.15);
$color-default-black-010:         rgba(#000000, 0.1);
$color-default-white:             #ffffff;
$color-default-white-090:         rgba(#ffffff, 0.9);
$color-default-white-030:         rgba(#ffffff, 0.3);
$color-transparent:               rgba(255, 255, 255, 0);

// Base

$color-black:                     #303030;
$color-black-070:                 rgba(#303030, 0.7);
$color-black-060:                 rgba(#303030, 0.6);
$color-black-040:                 rgba(#303030, 0.4);
$color-pink-swan:                 #b3b3b3;
$color-silver:                    #bfbfbf;
$color-silver-090:                rgba(#bfbfbf, 0.9);
$color-silver-020:                rgba(#bfbfbf, 0.2);
$color-silver-2:                  #c5c5c5;
$color-silver-2-090:              rgba(#c5c5c5, 0.9);
$color-silver-2-020:              rgba(#c5c5c5, 0.2);
$color-very-light-grey:           #cccccc;
$color-very-light-grey-090:       rgba(#cccccc, 0.9);
$color-very-light-grey-050:       rgba(#cccccc, 0.5);
$color-very-light-grey-030:       rgba(#cccccc, 0.3);
$color-very-light-grey-020:       rgba(#cccccc, 0.2);
$color-gainsboro:                 #d9d9d9;
$color-gainsboro-090:             rgba(#d9d9d9, 0.9);
$color-gainsboro-050:             rgba(#d9d9d9, 0.5);
$color-gainsboro-020:             rgba(#d9d9d9, 0.2);
$color-gainsboro-2:               #dddddd;
$color-gainsboro-2-090:           rgba(#dddddd, 0.9);
$color-gainsboro-2-020:           rgba(#dddddd, 0.2);
$color-whisper:                   #e5e5e5;
$color-whisper-090:               rgba(#e5e5e5, 0.9);
$color-whisper-020:               rgba(#e5e5e5, 0.2);
$color-whisper-2:                 #eeeeee;
$color-white-smoke:               #f6f6f6;

$color-brick-red:                 #c62c3f;
$color-fire-engine-red:           #e11f21;

$color-tawny:                     #b36600;
$color-tawny-090:                 rgba(#b36600, 0.9);
$color-tawny-020:                 rgba(#b36600, 0.2);
$color-mango-tango:               #cb7400;
$color-mango-tango-090:           rgba(#cb7400, 0.9);
$color-mango-tango-020:           rgba(#cb7400, 0.2);
$color-tangerine:                 #ef8800;
$color-orange-peel:               #ff9c00;
$color-orange-peel-090:           rgba(#ff9c00, 0.9);
$color-orange:                    #ffaa00;
$color-orange-090:                rgba(#ffaa00, 0.9);

$color-grain-brown:               #d0b8a0;

$color-fun-green:                 #026634;
$color-green:                     #007519;
$color-green-015:                 rgba(#007519, 0.15);
$color-bilbao:                    #327908;
$color-bilbao-090:                rgba(#327908, 0.9);
$color-bilbao-020:                rgba(#327908, 0.2);
$color-bilbao-2:                  #398909;
$color-bilbao-2-020:              rgba(#398909, 0.2);
$color-bilbao-2-090:              rgba(#398909, 0.9);
$color-la-palma:                  #40990a;
$color-kelly-green:               #43a10b;
$color-kelly-green-3:             #46a90c;
$color-kelly-green-4:             #4db90d;
$color-kelly-green-4-090:         rgba(#4db90d, 0.9);
$color-kelly-green-2:             #54c90e;
$color-kelly-green-2-090:         rgba(#54c90e, 0.9);
$color-pixie-green:               #c2d9ae;

$color-atoll:                     #207d8D;
$color-atoll-090:                 rgba(#207d8D, 0.9);
$color-atoll-050:                 rgba(#207d8D, 0.5);
$color-atoll-020:                 rgba(#207d8D, 0.2);
$color-pelorous:                  #2aa7bc;
$color-summer-sky:                #35d1eb;
$color-summer-sky-090:            rgba(#35d1eb, 0.9);
$color-summer-sky-030:            rgba(#35d1eb, 0.3);

// Gradient

$footer-bg:                       linear-gradient(
                                    90deg,
                                    $color-white-smoke 50%,
                                    $color-kelly-green 50%
                                  );
$slider-left:                     linear-gradient(
                                    90deg,
                                    $color-default-white 0%,
                                    $color-transparent 100%
                                  );
$slider-left-050:                 linear-gradient(
                                    90deg,
                                    $color-default-white 50%,
                                    $color-transparent 100%
                                  );
$slider-rigth:                    linear-gradient(
                                    270deg,
                                    $color-default-white 0%,
                                    $color-transparent 100%
                                  );

$green-135:                       linear-gradient(
                                    135deg,
                                    $color-la-palma 0%,
                                    $color-kelly-green-3 100%
                                  );

// Shadows

$shadow--1:                       0 0 20px $color-default-black-025;
$shadow--2:                       inset 4px 4px 5px $color-default-black-030;
$shadow--3:                       0 0 10px $color-default-black-025;
$shadow--4:                       0 0 10px $color-default-black-015;
$shadow--5:                       0 0 4px $color-default-black-025;
$shadow--6:                       0 4px 4px $color-default-black-010;
$shadow--7:                       0 0 5px $color-default-black-025;

$complex-shadow--1:
  -5px 5px 10px $color-very-light-grey-020,
  5px -5px 10px $color-very-light-grey-020,
  -5px -5px 10px $color-default-white-090,
  5px 5px 13px $color-very-light-grey-090,
  inset 1px 1px 2px $color-default-white-030,
  inset -1px -1px 2px $color-very-light-grey-050;
$complex-shadow--2:
  -5px 5px 10px $color-tawny-020,
  5px -5px 10px $color-tawny-020,
  -5px -5px 10px $color-orange-090,
  5px 5px 13px $color-tawny-090;
$complex-shadow--3:
  inset -5px 5px 10px $color-tawny-020,
  inset 5px -5px 10px $color-tawny-020,
  inset -5px -5px 10px $color-orange-090,
  inset 5px 5px 13px $color-tawny-090;
$complex-shadow--4:
  -5px 5px 10px $color-bilbao-020,
  5px -5px 10px $color-bilbao-020,
  -5px -5px 10px $color-kelly-green-2-090,
  5px 5px 13px $color-bilbao-090;
$complex-shadow--5:
  inset -5px 5px 10px $color-bilbao-020,
  inset 5px -5px 10px $color-bilbao-020,
  inset -5px -5px 10px $color-kelly-green-2-090,
  inset 5px 5px 13px $color-bilbao-090;
$complex-shadow--6:
  -5px 5px 10px $color-silver-2-020,
  5px -5px 10px $color-silver-2-020,
  -5px -5px 10px $color-default-white-090,
  5px 5px 13px $color-silver-2-090;
$complex-shadow--7:
  inset -5px 5px 10px $color-silver-020,
  inset 5px -5px 10px $color-silver-020,
  inset -5px -5px 10px $color-default-white-090,
  inset 5px 5px 13px $color-silver-090;
$complex-shadow--8:
  -5px 5px 10px $color-gainsboro-020,
  5px -5px 10px $color-gainsboro-020,
  -5px -5px 10px $color-default-white-090,
  5px 5px 13px $color-gainsboro-090,
  inset 1px 1px 2px $color-default-white-030,
  inset -1px -1px 2px $color-gainsboro-050;
$complex-shadow--9:
  -5px 5px 10px $color-bilbao-2-020,
  5px -5px 10px $color-bilbao-2-020,
  -5px -5px 10px $color-kelly-green-4-090,
  5px 5px 13px $color-bilbao-2-090;
$complex-shadow--10:
  -5px 5px 10px $color-mango-tango-020,
  5px -5px 10px $color-mango-tango-020,
  -5px -5px 10px $color-orange-peel-090,
  5px 5px 13px $color-mango-tango-090;
$complex-shadow--11:
  -5px 5px 10px $color-atoll-020,
  5px -5px 10px $color-atoll-020,
  -5px -5px 10px $color-summer-sky-090,
  5px 5px 13px $color-atoll-090,
  inset 1px 1px 2px $color-summer-sky-030,
  inset -1px -1px 2px $color-atoll-050;
$complex-shadow--12:
  1px 1px 2px $color-summer-sky-030,
  -1px -1px 2px $color-atoll-050,
  inset -5px 5px 10px $color-atoll-020,
  inset 5px -5px 10px $color-atoll-020,
  inset -5px -5px 10px $color-summer-sky-090,
  inset 5px 5px 13px $color-atoll-090;
$complex-shadow--13:
  -5px 5px 10px $color-whisper-020,
  5px -5px 10px $color-whisper-020,
  -5px -5px 10px $color-default-white-090,
  5px 5px 13px $color-whisper-090;
$complex-shadow--14:
  -5px 5px 10px $color-gainsboro-2-020,
  5px -5px 10px $color-gainsboro-2-020,
  -5px -5px 10px $color-default-white-090,
  5px 5px 13px $color-gainsboro-2-090;
$complex-shadow--15:
  inset -3px 3px 6px $color-gainsboro-020,
  inset 3px -3px 6px $color-gainsboro-020,
  inset -3px -3px 6px $color-default-white-090,
  inset 3px 3px 8px $color-gainsboro-090;

// Typography
//---------------------------------

$font-family--roboto:             "Roboto", "Arial", sans-serif;
$font-family--rubik:              "Rubik", "Arial", sans-serif;

// Animation
//---------------------------------

$trans--200:                      0.2s linear;
$trans--300:                      0.3s ease;
$trans--600:                      0.6s cubic-bezier(0.55, 0, 0.1, 1);

// Viewports
//---------------------------------

$vp--476:                         476px;
$vp--600:                         600px;
$vp--768:                         768px;
$vp--1024:                        1024px;
$vp--1280:                        1280px;
