.slider-reviews {
  position: relative;
  padding: 0 60px;


  @include vp--599 {
    padding: 0;
    padding-bottom: 60px;
  }
}

.slider-reviews__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;

  &.slider-btn--prev {
    left: 0;
    transform: translateY(-50%) rotate(-180deg);
  }

  &.slider-btn--next {
    right: 0;
  }

  @include vp--599 {
    top: unset;
    bottom: 0;
    transform: unset;

    &.slider-btn--prev {
      left: calc(50% - 50px);
      transform: rotate(-180deg);
    }

    &.slider-btn--next {
      right: calc(50% - 50px);
    }
  }
}
