.footer-menu {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;

  padding: 40px 0 80px;

  @include vp--1023 {
    padding-bottom: 24px;
  }
}

.footer-menu__item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.footer-menu__link {
  display: block;

  width: fit-content;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
}

.footer-menu__sublist {
  @include reset-list;

  color: $color-black-070;
}
