.btn {
  @include reset-btn;

  display: flex;
  justify-content: center;
  align-items: center;

  min-width: 240px;
  width: fit-content;
  min-height: 50px;
  padding: 19px;
  padding-top: 4px;
  padding-bottom: 4px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;

  color: $color-kelly-green;
  background-color: $color-default-white;
  border: 1px solid $color-kelly-green;
  border-radius: 25px;
  box-shadow: $complex-shadow--6;

  animation: btn-default $trans--200;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      padding-top: 6px;
      padding-bottom: 2px;

      box-shadow: $complex-shadow--7;

      animation: btn-default-hover $trans--200;
    }
  }

  &--bright {
    color: $color-default-white;
    background-color: $color-tangerine;
    border-color: $color-default-white;
    box-shadow: $complex-shadow--10;

    animation: btn-bright $trans--200;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        padding-top: 6px;
        padding-bottom: 2px;

        box-shadow: $complex-shadow--3;

        animation: btn-bright-hover $trans--200;
      }
    }
  }

  &--calm {
    color: $color-default-white;
    background-color: $color-pelorous;
    border-color: $color-default-white;
    box-shadow: $complex-shadow--11;

    animation: btn-calm $trans--200;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        padding-top: 6px;
        padding-bottom: 2px;

        box-shadow: $complex-shadow--12;

        animation: btn-calm-hover $trans--200;
      }
    }
  }

  &--contrast {
    padding-top: 5px;
    padding-bottom: 3px;

    color: $color-default-white;
    background-color: $color-kelly-green;
    border-color: $color-kelly-green;
    box-shadow: $shadow--2;

    animation: none;

    transition:
      background-color $trans--300,
      border-color $trans--300;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        padding-top: 5px;
        padding-bottom: 3px;

        background-color: $color-tangerine;
        border-color: $color-tangerine;
        box-shadow: $shadow--2;

        animation: none;
      }
    }
  }

  &--contrast-inverted {
    background-color: $color-tangerine;
    border-color: $color-tangerine;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-kelly-green;
        border-color: $color-kelly-green;
      }
    }
  }

  &--flat {
    min-height: unset;
    padding-top: 10px;
    padding-bottom: 8px;

    font-size: 14px;
    line-height: 1.43;

    box-shadow: none;
    animation: none;

    transition:
      background-color $trans--300,
      border-color $trans--300;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        padding-top: 10px;
        padding-bottom: 8px;

        box-shadow: none;
        animation: none;

        transition:
          background-color $trans--300,
          border-color $trans--300;
      }
    }
  }
}

@keyframes btn-default {
  0% {
    padding-top: 6px;
    padding-bottom: 2px;

    box-shadow: $complex-shadow--7;
  }

  50% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: none;
  }

  100% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: $complex-shadow--6;
  }
}

@keyframes btn-default-hover {
  0% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: $complex-shadow--6;
  }

  50% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: none;
  }

  100% {
    padding-top: 6px;
    padding-bottom: 2px;

    box-shadow: $complex-shadow--7;
  }
}

@keyframes btn-bright {
  0% {
    padding-top: 6px;
    padding-bottom: 2px;

    box-shadow: $complex-shadow--3;
  }

  50% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: none;
  }

  100% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: $complex-shadow--10;
  }
}

@keyframes btn-bright-hover {
  0% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: $complex-shadow--10;
  }

  50% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: none;
  }

  100% {
    padding-top: 6px;
    padding-bottom: 2px;

    box-shadow: $complex-shadow--3;
  }
}

@keyframes btn-calm {
  0% {
    padding-top: 6px;
    padding-bottom: 2px;

    box-shadow: $complex-shadow--12;
  }

  50% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: none;
  }

  100% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: $complex-shadow--11;
  }
}

@keyframes btn-calm-hover {
  0% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: $complex-shadow--11;
  }

  50% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: none;
  }

  100% {
    padding-top: 6px;
    padding-bottom: 2px;

    box-shadow: $complex-shadow--12;
  }
}
