.product-categories {
  padding: 60px 0;

  color: $color-default-white;
  background-color: $color-kelly-green;

  @include vp--1023 {
    padding: 40px 0;
  }
}

.product-categories__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-transform: uppercase;
}

.product-categories__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px 20px;

  @include vp--1279 {
    grid-template-columns: repeat(3, 1fr);
  }

  @include vp--1023 {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  @include vp--767 {
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
  }
}

.product-categories__link {
  display: flex;
  align-items: center;

  width: fit-content;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.product-categories__icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 80px;
  height: 80px;
  padding: 15px;

  background-image: $green-135;
  border-radius: 50%;
  box-shadow: $complex-shadow--9;
}

.product-categories__desc {
  display: block;

  margin-left: 20px;

  span {
    font-size: 10px;
    line-height: 2;
  }
}
