.special-offers {
  display: flex;
  align-items: center;
  position: relative;

  min-height: 460px;
  padding: 60px 0;
}

.special-offers__slider {
  overflow: hidden;
  position: relative;
  z-index: 2;

  width: 380px;

  background: $color-default-white;
  border-radius: 5px;
  box-shadow: $shadow--3;

  @include vp--599 {
    width: 280px;
    font-size: 24px;
    line-height: 1.33;
  }
}
