.slider-price-dynamics__container {
  margin-left: 0;
  margin-right: 0;
  overflow: visible;
  list-style: unset;
  padding: 0;

  @include vp--599 {
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    list-style: none;
    padding: 0;
  }
}

.slider-price-dynamics__wrapper {
  transform: unset;
  width: unset;
  height: unset;
  transition-property: unset;
  box-sizing: border-box;
  position: static;

  justify-content: space-between;

  @include vp--599 {
    transform: translate3d(0,0,0);
    width: 100%;
    height: 100%;
    transition-property: transform,-webkit-transform;
    box-sizing: content-box;
    position: relative;

    justify-content: unset;
  }
}

.slider-price-dynamics__slide {
  flex-shrink: unset;
  position: static;
  transition-property: unset;

  width: 120px;

  &--wide {
    flex-grow: 1;
    width: 160px;
  }

  &--thin {
    width: 100px;
  }

  &:not(:last-child) {
    margin-right: 20px;
  }

  @include vp--767 {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  @include vp--599 {
    flex-shrink: 0;
    position: relative;
    transition-property: transform,-webkit-transform;

    width: 100px;

    &--wide {
      flex-grow: 0;
      width: calc(100% - 120px * 2);
    }

    &--thin {
      width: 100px;
    }

    &:not(:last-child) {
      margin-right: 0;
    }
  }

  @include vp--475 {
    &--wide {
      width: calc(100% - 120px * 1);
    }
  }
}

.slider-price-dynamics__head-cell {
  white-space: nowrap;

  margin: 0;
  margin-bottom: 20px;

  color: $color-kelly-green;
}

.slider-price-dynamics__cell {
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.slider-price-dynamics__pagination {
  display: flex;
  justify-content: center;
}

.slider-price-dynamics__bullet {
  width: 37px;
  height: 5px;
  margin-top: 20px;

  background-color: $color-whisper-2;
  border-radius: 2px;

  &:not(:last-child) {
    margin-right: 3px;
  }
}

.slider-price-dynamics__active-bullet {
  display: flex;

  min-width: 37px;
  width: max-content;

  background-color: none;
}

.slider-price-dynamics__active-bullet-inner {
  display: none;
  width: 37px;
  height: 5px;

  background-color: $color-kelly-green;
  border-radius: 2px;

  &:not(:first-child) {
    margin-left: 3px;
  }

  .slider-price-dynamics__active-bullet & {
    display: block;
  }

  @include vp--475 {
    &:nth-child(n + 3) {
      display: none;
    }
  }
}
