.brands-presentation {
  padding: 60px 0 40px;
}

.brands-presentation__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  color: $color-kelly-green;
}

.brands-presentation__subtitle {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-align: center;
  text-transform: uppercase;
}
