.faq {
  padding: 60px 0 100px;

  @include vp--1023 {
    padding-top: 40px;
  }
}

.faq__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.faq__questions {
  @include reset-list;

  margin-bottom: 40px;

  border-top: 1px solid $color-kelly-green;
}

.faq__question {
  padding: 20px 0;

  border-bottom: 1px solid $color-kelly-green;
}

.faq__btn {
  @include reset-btn;

  position: relative;

  width: 100%;
  padding-right: 40px;

  font-weight: 500;
  text-align: left;

  transition: color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-kelly-green;
    }
  }
}

.faq__btn-icon {
  @include reset-btn;
  position: absolute;
  top: 0;
  right: 0;

  width: 20px;
  height: 20px;
  padding: 0;

  border: none;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: $color-kelly-green;

    transition: transform $trans--300;
  }

  &::before {
    width: 18px;
    height: 2px;
  }

  &::after {
    width: 2px;
    height: 18px;
  }

  .faq__question.is-active & {
    &::before,
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}

.faq__answer-wrapper {
  padding-top: 20px;

  p {
    margin: 0;
  }
}
