.account-refund {
  padding: 20px;

  background-color: $color-white-smoke;
  border-radius: 10px;
  box-shadow: $complex-shadow--13;

  &--approved {
    background-color: $color-default-white;
  }

  &--closed {
    box-shadow: $complex-shadow--14;
  }

  &--rejected {
    box-shadow: $complex-shadow--14;
    background-color: $color-default-white;
  }
}

.account-refund__wrapper {
  display: flex;
  justify-content: space-between;

  > *:not(:last-child) {
    margin-right: 20px;
  }

  @include vp--767 {
    display: block;

    > *:not(:last-child) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
}

.account-refund__cell {
  flex: 1 1 160px;
}

.account-refund__data {
  width: max-content;
  margin: 0;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
}

.account-refund__desc {
  width: max-content;
  margin: 0;

  font-size: 14px;
  line-height: 1.43;
}

.account-refund__status {
  flex: 0 0 160px;

  margin: 0;
  padding: 10px 20px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
  text-align: center;

  color: $color-default-white;
  background: $color-tangerine;
  border-radius: 5px;

  .account-refund--approved & {
    background-color: $color-kelly-green;
  }

  .account-refund--closed & {
    background-color: $color-pixie-green;
  }

  .account-refund--rejected & {
    background-color: $color-fire-engine-red;
  }
}

.account-refund__footer {
  margin-top: 20px;
  padding-top: 20px;

  border-top: 1px solid $color-very-light-grey-090;
}

.account-refund__reason {
  margin: 0;

  font-size: 12px;
  line-height: 1.67;

  color: $color-black-060;
}
