.range-filter {
  width: 100%;
  max-width: 240px;
  padding: 0 6px;
}

.range-filter__slider {
  margin: 5px -6px;
  padding: 0 6px;

  &.noUi-horizontal {
    height: 2px;
    margin-bottom: 23px;

    background-color: $color-default-black-025;
    border: none;
    border-radius: 0;
    box-shadow: none;
  }

  .noUi-connect {
    background-color: $color-kelly-green;
  }

  .noUi-handle {
    cursor: pointer;
    top: 1px;
    right: 0;
    transform: translate(50%, -50%);
    width: 12px;
    height: 12px;

    background-color: $color-kelly-green;
    border: none;
    border-radius: 50%;
    box-shadow: none;

    &::before,
    &::after {
      content: unset;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-tangerine;
        outline: none;
      }
    }
  }
}

.range-filter__inputs {
  display: flex;
  justify-content: space-between;

  margin: 0 -6px;

  label {
    position: relative;

    width: calc(50% - 10px);
  }
}

.range-filter__label {
  position: absolute;
  top: 12px;
  left: 10px;
  z-index: 1;

  font-size: 10px;
  line-height: 1;
}

.range-filter__input {
  width: 100%;
  padding: 8px;
  padding-left: 27px;

  font-size: 12px;
  line-height: 1;

  outline: none;

  border: 1px solid $color-whisper;
  border-radius: 5px;

  transition: border-color $trans--300;

  &:hover,
  &:focus {
    border-color: $color-kelly-green;
  }
}
