.history {
  padding: 60px 0 100px;
}

.history__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.history__desc {
  margin: 0;
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 60px;
  }
}

.history__tabs-wrapper {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  margin-bottom: 40px;
}

.history__tabs {
  @include reset-list;
  position: relative;
  display: flex;

  width: fit-content;

  &::before {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    bottom: 9px;

    width: 100%;
    height: 2px;

    background-color: $color-kelly-green;
  }
}

.history__tab {
  width: 80px;

  &:not(:last-child) {
    margin-right: 10px;
  }
}



.history__tab-btn {
  @include reset-btn;
  display: flex;
  justify-content: flex-start;
  position: relative;
  height: 68px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;

  &::after {
    position: absolute;
    content: "";
    left: 15px;
    bottom: 0;

    width: 20px;
    height: 20px;

    background-color: $color-transparent;
    border-radius: 50px;
  }

  &.is-active {
    color: $color-kelly-green;

    &::after {
      background-color: $color-kelly-green;
    }

  }
}

.history__slide {
  display: flex;
  width: 100%;

  @include vp--599 {
    flex-direction: column;
  }
}

.history__slide-img {
  flex-shrink: 0;
  width: 280px;

  img {
    width: 100%;
  }

  @include vp--599 {
    width: 100%;
  }
}

.history__slide-desc {
  margin: 0;
  margin-left: 20px;

  @include vp--599 {
    margin-left: 0;
    margin-top: 20px;
  }
}
