.cart {
  padding: 60px 0 100px;

  @include vp--1023 {
    padding-top: 40px;
  }
}

.cart__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.cart__products-header {
  display: grid;
  grid-template-columns: 180px 180px 1fr minmax(160px, 1fr) 80px 80px minmax(100px, 1fr);
  gap: 20px;

  @include vp--1023 {
    display: none;
  }
}

.cart__products-header-cell {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
}

.cart__products {
  @include reset-list;

  margin-bottom: 20px;

  border-top: 1px solid $color-kelly-green;
  border-bottom: 1px solid $color-kelly-green;
}

.cart__product {
  margin-bottom: 20px;

  &:first-child {
    margin-top: 20px;
  }
}

.cart__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include vp--599 {
    flex-direction: column;
  }
}

.cart__summary {
  @include vp--599 {
    margin-bottom: 20px;
  }
}

.cart__order-price {
  display: block;

  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1,25;
}

.cart__order-price-value {
  margin-left: 20px;

  font-size: 20px;
  line-height: 1;
}

.cart__order-details {
  margin: 0;
  font-size: 12px;
  line-height: 1.67;

  color: $color-black;
}

.cart__order-actions {
  display: flex;
  flex-direction: row-reverse;

  @include vp--1023 {
    flex-direction: column;
  }

  &--empty-cart {
    margin-left: auto;

    @include vp--1023 {
      flex-direction: row-reverse;
    }

    @include vp--599 {
      flex-direction: column;

      margin-left: 0;
    }
  }
}

.cart__ordering {
  min-width: 180px;
  margin-left: 20px;

  @include vp--1023 {
    margin-left: 0;
    margin-bottom: 10px;
  }

  .cart__order-actions--empty-cart & {

    @include vp--1023 {
      margin-left: 20px;
      margin-bottom: 0;
    }

    @include vp--599 {
      margin-left: 0;
      margin-bottom: 10px;
    }
  }
}

.cart__add-produtc {
  min-width: 180px;

  transition:
    color $trans--300,
    border-color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
      border-color: $color-tangerine;

      transition:
        color $trans--300,
        border-color $trans--300;
    }
  }
}

.cart__message {
  margin-bottom: 20px;
  padding: 60px 0;

  border-top: 1px solid $color-kelly-green;
  border-bottom: 1px solid $color-kelly-green;
}

.cart__message-title {
  max-width: 360px;
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;
}

.cart__message-content {
  max-width: 360px;
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}
