.account-order__card {
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;

  padding: 20px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--13;

  .account-order--is-shipped & {
    background-color: $color-white-smoke;
    box-shadow: $complex-shadow--14;
  }

  > *:not(:last-child) {
    margin-right: 20px;
  }

  @include vp--767 {
    flex-direction: column;

    > *:not(:last-child) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
}

.account-order__details-link {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  &:not(:last-child) {
    margin-right: 0;
  }

  @include vp--767 {
    &:not(:last-child) {
      margin-bottom: 0;
    }
  }
}

.account-order__status {
  flex-shrink: 0;

  width: 160px;
  padding: 10px 20px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.43;
  text-align: center;

  color: $color-default-white;
  background: $color-kelly-green;
  border-radius: 5px;

  @include vp--767 {
    width: 100%;
  }
}

.account-order__data {
  flex-grow: 3;

  width: 160px;

  @include vp--767 {
    width: 100%;
  }
}

.account-order__title {
  margin: 0;

  @include vp--767 {
    text-align: center;
  }
}

.account-order__date {
  display: block;

  @include vp--767 {
    text-align: center;
  }
}

.account-order__cost {
  flex-grow: 2;

  width: 100px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;

  @include vp--767 {
    width: 100%;

    text-align: center;
  }
}

.account-order__price-list {
  flex-shrink: 0;

  width: 160px;

  @include vp--767 {
    width: fit-content;
  }
}

.account-order__price-list-desc {
  margin: 0;
  text-align: right;

  font-weight: 500;
}

.link.account-order__price-list-link {
  display: block;

  width: fit-content;
  margin-left: auto;

  color: $color-kelly-green;
  text-decoration: underline;

  transition: color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
    }
  }

  @include vp--767 {
    margin-left: 0;
  }
}

.account-order__re-order-btn {
  flex-shrink: 0;
  position: relative;
  z-index: 2;

  min-width: 160px;

  transition:
    color $trans--300,
    border-color $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
      border-color: $color-tangerine;

      transition:
        color $trans--300,
        border-color $trans--300;
    }
  }

  @include vp--767 {
    width: 100%;
  }
}

.account-order__details-title {
  margin: 20px 0;
}

.account-order__details-list {
  @include reset-list;

  margin-bottom: 20px;

  border-top: 1px solid $color-gainsboro;

  @include vp--767 {
    margin-bottom: 30px;
  }
}

.account-order__details-item {
  display: flex;
  align-items: center;
  padding: 20px;

  border-bottom: 1px solid $color-gainsboro;

  @include vp--767 {
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.account-order__position-title {
  flex-grow: 4;

  width: 360px;
  margin: 0;

  strong {
    font-weight: 500;
  }

  @include vp--767 {
    width: 100%;
    margin-bottom: 20px;
  }
}

.account-order__position-quantity {
  width: 100px;
  margin-left: 20px;

  font-size: 14px;
  line-height: 1.43;
  text-align: right;

  @include vp--767 {
    width: unset;
    margin-left: 0;
  }
}

.account-order__position-cost {
  flex-grow: 1;

  width: 110px;
  margin-left: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  text-align: right;

  @include vp--767 {
    flex-grow: unset;

    width: unset;
  }
}

.account-order__details-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 20px;

  @include vp--767 {
    display: block;

    padding: 0;
  }
}

.account-order__details-total {
  display: flex;
  align-items: center;

  @include vp--767 {
    margin-bottom: 30px;
  }
}

.account-order__details-total-desc {
  width: 60px;
  margin-right: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  @include vp--767 {
    width: unset;
  }
}

.account-order__details-total-cost {
  width: 120px;
  margin-right: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;

  @include vp--767 {
    width: unset;
    margin-right: 0;
  }
}
