.for-veterinarians {
  padding: 60px 0 40px;

  @include vp--1023 {
    padding: 40px 0 20px;
  }
}

.for-veterinarians__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.for-veterinarians__desc {
  margin: 0;
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 60px;
  }
}

.for-veterinarians__subtitle {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-transform: uppercase;
}
