.project-card {
  overflow: hidden;

  color: $color-default-white;

  border-radius: 5px;
  box-shadow: $complex-shadow--1;

  &:nth-child(n + 1) {
    background-color: $color-pelorous;
  }
}

.project-card__header {
  display: flex;
  justify-content: center;
  align-items: center;

  min-height: 90px;
  padding: 20px;

  background-color: $color-tangerine;

  li:nth-child(3n + 1) > .project-card & {
    background-color: $color-pelorous;
  }

  li:nth-child(3n + 3) > .project-card & {
    background-color: $color-brick-red;
  }

  @include vp--599 {
    padding: 20px 10px;
  }
}

.project-card__title {
  .project-card & {
    font-weight: 700;
    font-size: 20px;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
    color: $color-default-white;
  }
}

.project-card__footer {
  padding: 20px;

  background-color: $color-kelly-green;
}

.project-card__btn  {
  display: flex;
  justify-content: center;
  align-items: center;

  width: fit-content;
  min-width: 240px;
  margin: 0 auto;
  min-height: 50px;
  padding: 20px;
  padding-top: 3px;
  padding-bottom: 5px;

  font-weight: bold;
  font-size: 16px;
  line-height: 1.12;
  text-align: center;
  text-transform: uppercase;

  background-color: $color-kelly-green;
  border-radius: 25px;
  box-shadow: $complex-shadow--4;

  animation: partnership-card-btn $trans--200;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      padding-top: 5px;
      padding-bottom: 3px;

      box-shadow: $complex-shadow--5;
      animation: partnership-card-btn-hover $trans--200;
    }
  }
}

@keyframes partnership-card-btn {
  0% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: $complex-shadow--5;
  }

  50% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: none;
  }

  100% {
    padding-top: 3px;
    padding-bottom: 5px;

    box-shadow: $complex-shadow--4;
  }
}

@keyframes partnership-card-btn-hover {
  0% {
    padding-top: 3px;
    padding-bottom: 5px;

    box-shadow: $complex-shadow--4;
  }

  50% {
    padding-top: 4px;
    padding-bottom: 4px;

    box-shadow: none;
  }

  100% {
    padding-top: 5px;
    padding-bottom: 3px;

    box-shadow: $complex-shadow--5;
  }
}

