.exhibitions {
  padding: 60px 0 100px;
}

.exhibitions__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.exhibitions__desc {
  margin: 0;
  margin-bottom: 60px;
}

.exhibitions__list {
  @include reset-list;

  margin-bottom: 60px;
}

.exhibitions__item {
  &:not(:last-child) {
    margin-bottom: 60px;
  }
}

.exhibitions__item-title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-tangerine;
}
