.content {
  padding: 60px 0;

  &:nth-of-type(2n + 3) {
    background-color: $color-white-smoke;
  }

  &:last-of-type {
    padding-bottom: 100px;
  }

  &--air {
    height: 100px;
    padding: 0;

    &:last-of-type {
      padding-bottom: 0;
    }
  }

  h2,
  .h2,
  h3,
  p,
  ul:not([class]),
  ol:not([class]),
  img {
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  h2,
  .h2 {
    font-weight: 500;
    font-size: 24px;
    line-height: 1.17;
    text-transform: uppercase;
    color: $color-kelly-green;
  }

  h3 {
    color: $color-kelly-green;
  }


  ul:not([class]),
  ol:not([class]) {
    padding-left: 20px;
  }

  @include vp--1023 {
    padding: 40px 0;
  }
}

.content__text {
  &:not(:last-child) {
    margin-bottom: 60px;
  }
}

.content__group {
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;

  &:not(:last-child) {
    margin-bottom: 40px;
  }

  @include vp--1023 {
    grid-template-columns: 100%;
    justify-items: center;

    &--flat {
      gap: 40px 20px;
    }

    &--alt-mobile-size {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  @include vp--599 {
    &--alt-mobile-size {
      grid-template-columns: 100%;
    }
  }
}

.content__item {
  overflow: hidden;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--8;

  .content__group--flat & {
    background-color: transparent;
    border-radius: 0;
    box-shadow: none;
  }

  @include vp--1023 {
    .content__group & {
      max-width: 680px;
      width: 100%;
    }

    .content__group--flat & {
      max-width: unset;
      width: 100%;
    }

    .content__group--reverce &:first-child {
      grid-row-start: 2;
    }
  }
}

.content__desc {
  padding: 40px 20px;

  &--strict-title {
    h3 {
      color: $color-black;
    }
  }

  &--alt-color-title {
    h3 {
      color: $color-tangerine;
    }
  }

  .content__group--flat & {
    padding: 0;
  }
}

.content__img {
  height: 100%;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &--is-data {
    img {
      object-fit: contain;
    }
  }
}

.content__date {
  display: block;

  &:not(:last-child) {
    margin-bottom: 40px;

    @include vp--1023 {
      margin-bottom: 20px;
    }
  }
}
