.product-card {
  display: flex;
  flex-direction: column;

  height: 100%;

  .catalog__content--table & {
    flex-direction: row;
    align-items: flex-start;

    padding: 10px;
  }

  @include vp--767 {
    .catalog__content--table & {
      flex-direction: column;
    }
  }
}

.product-card__img{
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  margin-bottom: 20px;
  padding-top: percentage(340/280);

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: $color-default-white;
    width: 100%;
    height: 100%;

    object-fit: contain;
  }

  .catalog__content--table & {
    display: none;
  }
}

.product-card__title {
  order: 1;

  margin: 0;
  margin-bottom: 20px;

  .catalog__content--table & {
    display: none;
  }
}

.product-card__link {
  display: none;
  text-decoration: underline;

  .catalog__content--table & {
    display: block;
    flex: 0 3 380px;

    margin-bottom: 0;
    padding: 10px;
  }

  @include vp--767 {
    .catalog__content--table & {
      flex: 0 0 auto;
    }
  }
}

.product-card__title-link {
  margin: 0;
}

.product-card__price {
  display: flex;
  flex-wrap: wrap;

  margin-bottom: 20px;

  .catalog__content--table & {
    order: unset;
    flex: 0 2 200px;

    margin-bottom: 0;
    padding: 10px;
  }

  @include vp--767 {
    .catalog__content--table & {
      flex: 0 0 auto;
    }
  }
}

.product-card__price-inner {
  flex: 1 0 50%;

  padding-right: 10px;

  .catalog__content--table & {
    padding-right: 0;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}

.product-card__price-value {
  margin: 0;
  margin-bottom: 10px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;

  .catalog__content--table & {
    margin-bottom: 5px;
  }
}

.product-card__price-desc {
  margin: 0;

  font-size: 12px;
  line-height: 1.33;
  color: $color-black-060;
}

.product-card__wrapper {
  order: 1;

  .catalog__content--table & {
    flex-grow: 1;
    order: unset;
    display: flex;

    padding: 10px 0;
  }

  @include vp--767 {
    .catalog__content--table & {
      justify-content: space-between;

      width: 100%;
      padding-right: 10px;
    }
  }
}

.product-card__quanttity {
  display: flex;
  flex-wrap: wrap;

  margin-bottom: 20px;

  .catalog__content--table & {
    flex-shrink: 0;
    justify-content: space-between;

    width: 200px;
    margin-bottom: 0;
    padding: 0 10px;
  }

  @include vp--767 {
    .catalog__content--table & {
      flex: 0 0 auto;
      flex-direction: column;

      width: unset;
    }
  }
}

.product-card__quanttity-inner {
  flex: 1 0 50%;

  margin: 0;
  padding-right: 10px;

  font-size: 12px;
  line-height: 1.67;

  .catalog__content--large-tiles & {
    flex: 1 0 50%;
  }

  .catalog__content--small-tiles & {
    flex: 1 0 100%;
  }

  .catalog__content--table & {
    flex: 0 0 80px;
  }

  @include vp--767 {
    .catalog__content--table & {
      flex: 0 0 auto;
    }
  }
}

.product-card__quanttity-desc {
  .catalog__content--table & {
    display: none;
  }

  @include vp--767 {
    .catalog__content--table & {
      display: inline;
    }
  }
}

.product-card__btns {
  display: flex;

  margin-top: auto;

  .catalog__content--table & {
    flex-shrink: 0;
    justify-content: flex-end;

    width: 90px;
    margin-left: auto;
  }

  @include vp--767 {
    .catalog__content--table & {
      flex: 0 0 auto;
    }
  }
}

.product-card__btn-link {
  flex: 0 1 180px;

  min-width: unset;
  margin-right: 10px;

  .catalog__content--small-tiles & {
    margin-right: 5px;
    padding-left: 5px;
    padding-right: 5px;
  }

  .catalog__content--table & {
    display: none;

    margin-right: 0;
  }
}

.product-card__add-to-cart-btn {
  flex: 0 0 60px;

  min-width: unset;
  margin-left: 10px;

  .catalog__content--table & {
    margin-left: 0;
  }
}
