.slider-payment-schedule__container {
  margin-left: 0;
  margin-right: 0;
  overflow: visible;
  list-style: unset;
  padding: 0;

  @include vp--1279 {
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    list-style: none;
    padding: 0;
  }
}

.slider-payment-schedule__wrapper {
  transform: unset;
  width: unset;
  height: unset;
  transition-property: unset;
  box-sizing: border-box;
  position: static;

  justify-content: space-between;

  @include vp--1279 {
    transform: translate3d(0,0,0);
    width: 100%;
    height: 100%;
    transition-property: transform,-webkit-transform;
    box-sizing: content-box;
    position: relative;

    justify-content: unset;
  }
}

.slider-payment-schedule__slide {
  flex-shrink: unset;
  position: static;
  transition-property: unset;

  width: 100px;

  &--wide {
    width: 160px;
  }

  @include vp--1279 {
    flex-shrink: 0;
    position: relative;
    transition-property: transform,-webkit-transform;
  }

  @include vp--599 {
    &--wide {
      width: 120px;
    }
  }
}

.slider-payment-schedule__head-cell {
  white-space: nowrap;

  margin: 0;
  margin-bottom: 20px;

  font-size: 12px;
  line-height: 1.67;
  color: $color-kelly-green;
}

.slider-payment-schedule__cell {
  margin: 0;

  &--strong {
    font-weight: 500;
  }

  &--cost {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
  }

  &--accent {
    color: $color-fire-engine-red;
  }

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  @include vp--599 {
    font-size: 12px;
    line-height: 1.67;

    &--cost {
      font-size: 14px;
      line-height: 1.43;
    }
  }
}

.slider-payment-schedule__pagination {
  display: flex;
  justify-content: center;
}

.slider-payment-schedule__bullet {
  width: 37px;
  height: 5px;
  margin-top: 35px;

  background-color: $color-whisper-2;
  border-radius: 2px;

  &:not(:last-child) {
    margin-right: 3px;
  }
}

.slider-payment-schedule__active-bullet {
  display: flex;

  min-width: 37px;
  width: max-content;

  background-color: none;
}

.slider-payment-schedule__active-bullet-inner {
  display: none;
  width: 37px;
  height: 5px;

  background-color: $color-kelly-green;
  border-radius: 2px;

  &:not(:first-child) {
    margin-left: 3px;
  }

  .slider-payment-schedule__active-bullet & {
    display: block;
  }

  @include vp--767 {
    &:nth-child(n + 5) {
      display: none;
    }
  }

  @include vp--599 {
    &:nth-child(n + 4) {
      display: none;
    }
  }

  @include vp--475 {
    &:nth-child(n + 3) {
      display: none;
    }
  }
}
