.account-delivery-address {
  display: flex;
  flex-direction: column;

  min-height: 280px;
  height: 100%;
  padding: 20px;

  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--13;

  &--main {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: 20px;
      right: 20px;

      width: 20px;
      height: 20px;
      margin: 0 auto 10px;

      background-color: $color-kelly-green;
      background-image: url("../img/svg/icon-check-light-grey.svg");
      background-repeat: no-repeat;
      background-position: center;
      border-radius: 50%;
    }
  }
}

.account-delivery-address__title {
  margin: 0;
  margin-bottom: 20px;
  padding-right: 40px;
}

.account-delivery-address__address {
  margin: 0;
  margin-bottom: 20px;

  font-size: 14px;
  line-height: 1.43;
}

.link.account-delivery-address__links {
  margin-bottom: 20px;
}

.account-delivery-address__link {
  display: block;
  width: fit-content;

  font-size: 12px;
  line-height: 1.67;
  text-decoration: underline;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.account-delivery-address__note {
  margin-top: auto;

  font-size: 14px;
  line-height: 1.43;
}

.account-delivery-address__note-title {
  margin: 0;
  font-weight: 500;
}

.account-delivery-address__note-desc {
  margin: 0;
}

.account-delivery-address__note-placeholder {
  margin: 0;

  color: $color-black-060;
}
