.review-card__author {
  display: flex;
  align-items: center;

  margin-bottom: 10px;
}

.review-card__author-avatar {
  flex-shrink: 0;

  width: 60px;
  height: 60px;

  img {
    object-fit: cover;

    width: 100%;
    height: 100%;

    border-radius: 50px;
  }
}

.review-card__author-data {
  margin-left: 20px;
}

.review-card__author-name {
  margin: 0;

  font-weight: 500;
}

.review-card__category-name {
  margin: 0;

  font-weight: 300;
  font-size: 12px;
  line-height: 1.67;
}

.review-card__content {
  margin: 0;
  margin-bottom: 20px;
}

.review-card__footer {
  display: block;

  margin: 0;

  font-size: 12px;
  line-height: 1.67;
}

.review-card__rating {
  display: flex;
}

.review-card__rating-icon {
  color: $color-black-040;

  &--mark {
    color: $color-tangerine;
  }

  &:not(:last-child) {
    margin-right: 5px;
  }

  svg {
    display: block;
    width: 15px;
    height: 15px;
  }
}
