.custom-input {
  position: relative;

  display: flex;
  margin-bottom: 20px;

  label {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;

    input {
      flex-grow: 1;
      padding: 9px 19px;

      font-size: 14px;
      line-height: 1.43;
      color: inherit;

      border: 1px solid rgba($color-default-black, 0.2);
      border-radius: 5px;
      outline: none;

      transition: border-color $trans--300;

      &:hover,
      &:focus {
        border-color: $color-kelly-green;
      }
    }

    @include vp--599 {
      flex-direction: column;
      align-items: stretch;
    }
  }

  &__label {
    cursor: pointer;
    width: 180px;
    margin-right: 20px;

    font-size: 14px;
    line-height: 1.43;

    label:hover &,
    label:focus & {
      color: $color-kelly-green;
    }

    @include vp--599 {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &__error {
    position: absolute;
    right: 0;
    top: 100%;

    font-size: 12px;
    line-height: 1.33;
    color: $color-fire-engine-red;

    opacity: 0;

    transition: opacity $trans--300;
  }

  &.is-invalid {
    input {
      border-color: $color-fire-engine-red;

      &:focus,
      &:hover {
        border-color: $color-fire-engine-red;
      }
    }

    .custom-input__error {
      opacity: 1;
    }
  }

  // &.is-valid {
  //   input {
  //     border: 1px solid green;

  //     &:focus,
  //     &:hover {
  //       border: 1px solid rgba(green, 0.5);
  //     }
  //   }
  // }
}
