.reviews {
  padding: 60px 0 100px;

  background-color: $color-green-015;

  &--simple {
    padding: 0;
    padding-bottom: 60px;

    @include vp--1023 {
      padding-bottom: 40px;
    }

    background-color: transparent;
  }
}

.reviews__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  .reviews--simple & {
    font-size: 20px;
    line-height: 1.2;
    text-align: left;
    text-transform: none;

    color: $color-tangerine;
  }
}
