.franchise__title {
  .franchise & {
    color: $color-tangerine;
  }
}

.franchise__features-list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(auto-fill, 220px);
  gap: 20px;

  @include vp--599 {
    grid-template-columns: 100%;
  }
}

.franchise__features-desc {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
}

.franchise__features-list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(auto-fill, 220px);
  gap: 20px;
}

.franchise__features-item {
  display: flex;
  flex-direction: column;
  align-items: center;

  svg {
    flex-shrink: 0;
    width: 140px;
    height: 140px;
    margin-bottom: 20px;
  }

  @include vp--599 {
    flex-direction: row;

    svg {
      width: 100px;
      height: 100px;
      margin-right: 20px;
      margin-bottom: 0;
    }
  }
}

.franchise__features-item-desc {
  margin: 0;

  text-align: center;

  @include vp--599 {
    text-align: left;
  }
}
