.slider-brands {
  position: relative;

  width: 1180px;
  margin: 0 auto;

  @include vp--1279 {
    width: 820px;
  }

  @include vp--1023 {
    width: 640px;
  }

  @include vp--767 {
    width: 460px;
  }

  @include vp--599 {
    width: 280px;
  }
}

.slider-brands__container {
  position: relative;
  width: 1090px;
  margin: 0 auto;
  padding: 20px 15px;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    width: 10px;
    z-index: 2;
  }

  &::before {
    left: 0;

    background-image: $slider-left;
  }

  &::after {
    right: 0;

    background-image: $slider-rigth;
  }

  @include vp--1279 {
    width: 730px;
  }

  @include vp--1023 {
    width: 550px;
  }

  @include vp--767 {
    width: 370px;
  }

  @include vp--599 {
    width: 190px;
  }
}

.slider-brands__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;

  &.slider-btn--prev {
    left: 0;
    transform: translateY(-50%) rotate(-180deg);
  }

  &.slider-btn--next {
    right: 0;
  }
}
