.similar-products {
  padding-bottom: 100px;
}

.similar-products__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  margin-bottom: 20px;
}

.similar-products__title {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  color: $color-kelly-green;
}

.similar-products__link {
  flex-shrink: 0;

  width: max-content;
  margin-left: 20px;

  font-size: 20px;
  line-height: 1.2;
  text-decoration-line: underline;

  color: $color-kelly-green;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover[href],
    &:focus {
      color: $color-tangerine;
    }
  }
}

.similar-products__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px 20px;

  @include vp--1023 {
    grid-template-columns: repeat(3, 1fr);
  }

  @include vp--767 {
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp--599 {
    grid-template-columns: repeat(1, 1fr);
  }
}

.similar-products__item {
  &:nth-child(n + 5) {
    display: none;
  }

  @include vp--1023 {
    &:nth-child(4) {
      display: none;
    }
  }

  @include vp--767 {
    &:nth-child(3) {
      display: none;
    }
  }

  @include vp--599 {
    &:nth-child(2) {
      display: none;
    }
  }
}
