.modal-autentification__title {
  position: relative;
  margin: 0;
  margin-bottom: 20px;
  padding: 0 30px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    background-color: $color-kelly-green;
    background-image: url("../img/svg/icon-check-light-grey.svg");
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
  }
}

.modal-autentification__desc {
  margin: 0;
}
