.contacts {
  padding-bottom: 100px;
}

.contacts__map {
  margin-bottom: 60px;
  iframe {
    width: 100%;
    height: 460px;
    border: 0;
  }
}

.contacts__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.contacts__content {
  margin: -20px;
  padding: 20px;
}

.contacts__tabs-wrapper {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  margin-bottom: 40px;
}

.contacts__tabs {
  @include reset-list;
  position: relative;
  display: flex;

  width: fit-content;
  min-width: 100%;
  padding-bottom: 15px;

  &::before {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    bottom: 1px;

    width: 100%;
    height: 1px;

    background-color: $color-whisper;
  }
}

.contacts__tab {
  &:not(:last-child) {
    padding-right: 80px;
  }

  @include vp--599 {
    &:not(:last-child) {
      padding-right: 20px;
    }
  }
}

.contacts__tab-btn {
  @include reset-btn;
  position: relative;

  font-size: 16px;
  line-height: 1.5;
  color: $color-black-060;

  width: max-content;

  transition: color $trans--300;

  &::before {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    bottom: -15px;

    height: 4px;

    background-color: transparent;
    border-radius: 2px;

    transition: background-color $trans--300;
  }

  &.is-active {
    cursor: auto;
    color: $color-black;

    &::before {
      background-color: $color-tangerine;
    }
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:not(.is-active):hover,
    &:focus {
      color: $color-tangerine;
    }
  }

  @include vp--599 {
    font-size: 14px;
    line-height: 1.43;
  }
}

.contacts__wrapper {
  display: flex;
  align-items: flex-start;

  @include vp--1023 {
    display: block;
  }
}

.contacts__locations {
  flex-grow: 1;

  margin-right: 20px;

  transition: height $trans--300;

  @include vp--1023 {
    margin-right: 0;
    margin-bottom: 40px;
  }
}

.contacts__division {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.contacts__location-item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.contacts__address-title {
  margin: 0;

  font-weight: 500;
}

p.contacts__address {
  margin: 0;
}

.link.contacts__phone {
  display: block;

  font-weight: 500;
}

.link.contacts__email {
  display: block;

  color: $color-kelly-green;
  text-decoration: underline;

  .contacts__department & {
    display: inline;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-tangerine;
    }
  }
}

.contacts__department {
  margin: 0;
}

.contacts__region {
  margin: 0;

  font-weight: 500;
}

.contacts__chief-position {
  margin: 0;

  font-size: 12px;
  line-height: 1.67;
}

.contacts__chief-name{
  margin: 0;

  font-weight: 500;
}

.contacts__form {
  flex-shrink: 0;

  width:680px;
  padding: 40px;

  background: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--8;

  @include vp--1279 {
    width: 100%;
    max-width: 628px;
  }

  @include vp--599 {
    padding: 40px 20px;
  }
}

.contacts__form-title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-transform: uppercase;
  color: $color-kelly-green;
}

.contacts__form-desc {
  margin: 0;
  margin-bottom: 20px;

  font-size: 14px;
  line-height: 1.43;
}
