.header {
  position: relative;
  z-index: 15;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;

    pointer-events: none;

    box-shadow: $shadow--6;
  }

  &--stretch {
    display: flex;
    flex-direction: column;
  }
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 15px 0;

  border-bottom: 1px solid $color-kelly-green;

  @include vp--1023 {
    border-bottom: none;
  }

  @include vp--599 {
    padding: 10px 0;
    align-items: flex-end;
  }
}

.header__logo {
  img {
    width: 160px;
  }

  @include vp--599 {
    margin: 10px 0;

    img {
      width: 100px;
    }
  }
}

.header__menu {
  @include vp--599 {
    margin: -10px 0;
  }
}
