.page-header {
  position: relative;
  min-height: 460px;

  color: $color-default-white;

  @include vp--1279 {
    min-height: 306px;
  }

  @include vp--1023 {
    min-height: 240px;
  }

  @include vp--767 {
    min-height: 184px;
  }
}

.page-header__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.page-header__wrapper {
  position: relative;
  z-index: 2;

  padding: 30px 0 40px;

  @include vp--1023 {
    padding: 10px 0 20px;
  }
}
