.partnership-offer__features {
  padding: 60px 0 40px;

  @include vp--1023 {
    padding: 40px 0;
  }
}

.partnership-offer__directions {
  padding: 40px 0 60px;

  background-color: $color-white-smoke;

  @include vp--1023 {
    padding: 40px 0;
  }
}

.partnership-offer__title {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.11;
  text-transform: uppercase;
}

.partnership-offer__subtitle {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: $color-tangerine;
}

.partnership-offer__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;

  @include vp--1023 {
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp--767 {
    grid-template-columns: repeat(1, 1fr);
  }

  @include vp--599 {
    gap: 10px;
  }
}

.partnership-offer__item {
  overflow: hidden;
  display: flex;
  align-items: center;

  background: $color-default-white;
  border-radius: 10px;
  box-shadow: $complex-shadow--8;
}

.partnership-offer__icon {
  flex-shrink: 0;
  align-self: stretch;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100px;
  min-height: 100px;
  padding: 10px;

  color: $color-default-white;
  background-color: $color-tangerine;

  @include vp--599 {
    width: 80px;
  }
}

.partnership-offer__counter {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 60px;
  height: 60px;

  border: 3px solid $color-default-white;
  border-radius: 50%;

  span {
    font-weight: 500;
    font-size: 36px;
    line-height: 1;
    text-transform: uppercase;
  }

  @include vp--599 {
    width: 40px;
    height: 40px;

    span {
      font-size: 24px;
    }
  }
}

p.partnership-offer__desc {
  margin: 0;
  padding: 10px 20px;
}
