.footer {
  background-color: $color-white-smoke;
  background-image: $footer-bg;

  @include vp--1023 {
    background-image: none;
  }
}

.footer__top-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;

  @include vp--1023 {
    display: block;
  }
}

.footer__menu {
  grid-column: span 3;

  background-color: $color-white-smoke;

  @include vp--599 {
    display: none;
  }
}

.footer__copyright {
  padding: 20px 0;

  font-size: 12px;
  line-height: 1.67;

  color: $color-default-white;
  background-color: $color-tangerine;
}

.footer__copyright-container {
  display: flex;
  justify-content: space-between;

  @include vp--599 {
    display: block;
  }
}

.footer__privacy-policy {
  width: percentage(1 / 3);
  padding-right: 40px;

  @include vp--1023 {
    width: 50%;
  }

  @include vp--599 {
    width: 100%;
    margin-bottom: 40px;
  }
}

.footer__privacy-policy-link {
  display: block;

  width: fit-content;
}

.footer__privacy-policy-owner {
  margin: 0;
}

.footer__notifications {
  width: percentage(2 / 3);

  @include vp--1023 {
    width: 50%;
  }

  @include vp--599 {
    width: 100%;

    font-size: 10px;
    line-height: 1.4;
  }
}

.footer__notification {
  margin: 0;

  text-align: right;

  @include vp--1023 {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  @include vp--599 {
    text-align: left;
  }
}
